<style type="text/css" scoped="firm">
  .bounce-enter-active {
    -webkit-transform: scale(2);
    -moz-transform: scale(2);
    -ms-transform: scale(2);
    transform: scale(2);
    opacity: 0;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
  }

  .bounce-leave-active {
    -webkit-transform: scale(2);
    -moz-transform: scale(2);
    -ms-transform: scale(2);
    transform: scale(2);
    opacity: 0;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
  }

  .yy-confirm {
    position: fixed;
    top: 0;
    left: 0;
    width: 1280px;
    height: 720px;
    z-index: 9999999;
  }

  .yy-confirm .bg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.3;
    z-index: 99999;
  }

  .yy-confirm .yy-container {
    position: absolute;
    display: inline-block;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 1280px;
    z-index: 9999999999999;
    width: 600px;
    border: 1px solid white;
  }

  .yy-confirm .yy-container .header {
    height: 50px;
    padding-left: 30px;
    line-height: 50px;
    background-color: rgba(21, 48, 57, 0.9);
    border-bottom: 1px rgba(0, 128, 128, 0.95) solid;
    color: #fff;
    text-align: left;
    box-sizing: border-box;
    margin-top: 0px;
  }

  .yy-confirm .yy-container .yy-footer {
    background: rgba(34, 78, 96, 0.9);
    height: 300px;
    box-sizing: border-box;
  }

  .yy-btn {
    text-decoration: none !important;
    border-radius: 14px;
    text-align: center;
    width: 30%;
    float: left;
    margin-top: 120px;
    margin-left: 80px;
    height: 50px;
    line-height: 50px;
    border: 2px white solid;
    background-color: #152F3A;
    color: #fff;
  }

  .yy-btn.select-on {
    background-color: #992121;
    color: #fff;
  }

  .yy-close-btn {
    cursor: pointer;
    float: right;
  }

</style>

<template>
  <div class="yy-confirm" v-show="visible">
    <div class="bg"></div>
    <div class="yy-container" :style="{width: width}">
      <div class="header" v-if="mx">
        <i class="iconfont icon-wenhao" style="font-size: 22px;line-height: 50px"></i>
        {{title.value}}
      </div>

      <div class="header" v-else>
         <i class="iconfont icon-wenhao" style="font-size: 22px;line-height: 50px"></i>
        
      </div>
      <slot name="footer">
        <div class="yy-footer" slot="footer">
          <a href="javscript:void(0)" class="yy-btn make-sure select-on" v-if="mx">{{title.value2}}
            <i class="iconfont icon-tuichu" style="font-size: 22px;line-height: 50px"></i>
          </a>
          <a href="javscript:void(0)" class="yy-btn make-sure select-on" v-else>
            <i class="iconfont icon-tuichu" style="font-size: 22px;line-height: 50px"></i>
          </a>

          <a href="javscript:void(0)" class="yy-btn cancel" v-if='mx'>{{title.value3}}
            <i class="iconfont icon-iconfontfiletext" style="font-size: 22px;line-height: 50px"></i>
          </a>

          <a href="javscript:void(0)" class="yy-btn cancel" v-else>
            <i class="iconfont icon-iconfontfiletext" style="font-size: 22px;line-height: 50px"></i>
          </a>
        </div>
      </slot>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
  </div>
</template>

<script>
  import {
    mapGetters
  } from 'vuex'
  import {
    HTTPUtil as api
  } from '../../fetch/api.js'
  import {
    client as yh
  } from '../../js/client.js'
  import c from '../../js/common.js'
  import {
    bp as bi
  } from '../../js/ga.js'

  export default {
    data: function () {
      return {
        width: {
          type: String,
          value: '360px'
        },
        title: {
          type: String,
          value: '',
          value2: '',
          value3: ''
        },
        isShow: false,
        addRemver: 0,
        catCode: "",
        act: true,
        num: 0,
        urlss: '',
        name: "CFirm",
        jsonUrl: '',
        asID: '',
        usename: '',
        zt: true,
        mx: true,
        visible: false,
        enterType:4
      }
    },
    methods: {
      close() {
        this.$emit('update:visible', false)
      },
      //      asset_id, trace_id, trace_type, special_type, type
      favColl() {
        let assetId = this.asID;
        //          
        let traceId = c.getTrackPath();
        //          
        let traceType = c.getTrackPathType();
        bi.collectPage(assetId, traceId, traceType, this.addRemver);
      },
      hisColl() {
        let assetId = this.asID;
        //          
        let traceId = c.getTrackPath();
        //          
        let traceType = c.getTrackPathType();
        bi.historyClick(assetId, this.addRemver, traceId, traceType);
      },
      keyCode(kc) {
        if (kc == "left") {
          this.left();
        } else if (kc == "right") {
          this.right();
        } else if (kc == "KeyEnter") {
          this.KeyEnter();
        } else if (kc == "KeyBack") {
          this.KeyBack();
        }
      },
      KeyBack() {
        this.visible = false;
        this.isShow = false;
        console.log(this.usename);
        this.$emit("listernpopup", false, this.usename, 'back');
      },
      KeyEnter() {
        this.visible = false;
        this.isShow = false;
        if (this.num == 0) {
          //          
          this.addRemver = 1;
            this.favColl();
            let obj ={
              enterType: this.enterType
            }
          if (!this.zt) {
            this.favColl();
            if (this.act) {
              //              
              c.routerSkip(this.jsonUrl, "1", obj, this.$router);
              window.localStorage.setItem('EnterType','3');
            } else {
              c.routerSkip(this.jsonUrl, "2", '', this.$router);
            }
          } else {
            if (this.urlss.assetType == '') {
              c.routerSkip(this.jsonUrl, '5', '', this.$router);
            } else {
              c.routerSkip(this.jsonUrl, '4', '', this.$router);
            }
          }
          if (this.usename == 0) {
            this.favColl();
          } else {
            this.hisColl();
          }
        } else {
          this.addRemver = 2;
          //          
          if (this.usename == 0) {
            this.favColl();
            if (!this.zt) {
              //              ""
              let storage = JSON.parse(localStorage.getItem('PersonMovies'));
              let json = [];
              for (let i = 0; i < storage.length; i++) {
                if (i != this.findindex()) {
                  json.push(storage[i]);
                }
              };
              localStorage.setItem('PersonMovies', JSON.stringify(json));
            } else {
              //              ""
              let storage = JSON.parse(localStorage.getItem('PersonInfo'));
              let json = [];
              for (let i = 0; i < storage.length; i++) {
                if (i != this.findindexs()) {
                  json.push(storage[i]);
                }
              };
              localStorage.setItem('PersonInfo', JSON.stringify(json));
            }
          } else {
            this.hisColl();
            yh.removeHistorys(this.asID);
          }
          this.$emit("listernpopup", false, this.usename, 'remove');
        }
      },
      findindex() {
        let storage = JSON.parse(localStorage.getItem('PersonMovies'));
        for (let i = 0; i < storage.length; i++) {
          if (storage[i].assetId == this.asID) {
            return i;
          }
        }
      },
      findindexs() {
        let storage = JSON.parse(localStorage.getItem('PersonMovies'));
        for (let i = 0; i < storage.length; i++) {
          if (storage[i].specialId == this.asID) {
            return i;
          }
        }
      },
      right() {
        this.num++;
        if (this.num > 1) {
          this.num = 1
        }
        $('.make-sure').removeClass('select-on');
        $('.cancel').addClass('select-on');
      },
      left() {
        this.num--;
        if (this.num < 0) {
          this.num = 0
        }
        $('.cancel').removeClass('select-on');
        $('.make-sure').addClass('select-on');
      },
      updateStast(url, Id, name, type, actor) {
        console.log(url, Id, name + '');
        this.num = 0;
        $('.cancel').removeClass('select-on');
        $('.make-sure').addClass('select-on');
        this.isShow = true;
        this.visible = true;
        this.urlss = url;
        this.jsonUrl = url.layoutUrl;
        this.asID = url.assetId;
        this.catCode = type;
        this.usename = name;
        this.act = actor;
        if (url.assetType == "" || url.assetType == "") {
          this.zt = true;
        } else {
          this.zt = false;
        }
        if (url.assetType == "") {
          this.mx = true;
        } else {
          this.mx = false;
        }
      },
    },
    components: {

    }
  }

</script>



