<!--  -->
<template>
    <div class="search">
        <!--  -->
       <fi></fi>
        <div class="returnKey">
            
        </div>
        <div class="search-left">
            <h3>{{title}}</h3>
            <kb v-on:pressDown="keepDo" ref="kbc" :all-show="allShow" nav-show="navShow" :type='type' :ban="ban"></kb>
            <div class="tip"></div>
        </div>
        <div class="search-right">
            <div class="navJ-box">
                <div class="navJ" v-if="navShow">
                    <div id="n0" class="hover">
                        <p style="display:none">all</p>
                    </div>
                    <div v-for="(item,index) in categoryArr" :key="index" :id="('n'+(index+1))">{{item.categoryName}}
                        <p class="type" style="display:none">{{item.categoryType}}</p>
                    </div>
                </div>
            </div>
            <div v-if="isRecommend" class="recommend"></div>
            <template v-if="isRecommend">
                <div v-for="(item,index,key) in eleList" :key="item.id" :id="('a'+key)" :class="index=='hotActorList'?'search_actor':'search_ul'">
                    <div class="search_year">
                        <div></div>
                        <p>{{index=='hotActorList'?'':''}}</p>
                        <span>
                            <b>0</b> / {{item.length}}</span>
                    </div>
                    <div class="searchImgs">
                        <div class="search_img" v-for="(assert,sk) in item" :key="assert.key">
                            <div class="inner_box" :id="('b'+sk)">
                                <lazy-image v-if="index=='hotActorList'" :src='assert.elementImg' :placeholder='placeholder' :imgClass="circle" />
                                <lazy-image v-else :src='assert.elementImg' :placeholder='placeholder' :initScore='initScore' :title="assert.elementName" :score="assert.score" />
                            </div>
                            <div class="listname">
                                <div style="display: inline;height: 100%;">{{assert.elementName}}</div>
                                <div style="display: inline;height: 100%;"></div>
                            </div>
                        </div>
                    </div>
                </div>
            </template>
            <template v-else>
                <div>
                    <div class="assetJ">
                        <div class="item_box" v-for="(item,index) in assetData" :key="index" :id="'n'+index">
                            <div :class="(index == itemNo?'inner_box active':'inner_box')">
                                <lazy-image :src='item.elementImg' :placeholder='placeholder' :initScore='initScore' :title="item.elementName" :score="item.score" :oragin = "item.providerId"  />
                            </div>
                            <div class="listname">
                                <div class="aJ" style="display: inline;height: 100%;">{{item.elementName}}</div>
                                <div class="bJ" style="display: inline;height: 100%;"></div>
                            </div>
                        </div>
                    </div>
                    <div class="activeNumber" v-show="!isSouth">
                        {{y+1}}/{{pageNum}}
                    </div>
                    <!--  -->
                    <div class="returnKey">
                        
                    </div>
                    <div class="scroll-wrap" v-if="scrollShow">
                        <div class="scroll-bar">
                        </div>
                    </div>
                </div>
            </template>
        </div>
        <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
    </div>
</template>

