<script>
import { mapGetters } from "vuex";
import kb from "../../tool/keyboard/keyboard.vue";
import { HTTPUtil as api } from "../../fetch/api.js";
import { South as south } from "../../fetch/south.js";
import { bp as bi } from "../../js/ga.js";
import c from "../../js/common.js";
import { client as yh } from "../../js/client";
import { xha as x } from "../../js/xha.js";
import lodash from 'lodash';
import Toast from '../../tool/toast/toast.js'
export default {
  props: ["jsonUrl", "type", "catId"],
  name: "search",
  data() {
    return {
      itemNo: -1,
      allShow: true,
      col: 0,
      row: 0,
      isShow: false,
      sc: false,
      title: "",
      placeholder: require("../../assets/img_loading_160x230.png"),
      initScore: require("../../assets/corner_mark.png"),
      eleList: [],
      keyword: "yh",
      oldF: [0, 0],
      newF: [],
      objindex: [],
      listLength: 0,
      circle: "circle",
      isRecommend: true,
      firstLoad: true,
      navShow: false,
      navPosition: false,
      n: 0,
      searchType: "all",
      activeN: "",
      enterType: 0,
      recommend: false,
      timer: null,
      reloadTimes: 0, //  
      assetData: [],
      activeNub: 0,
      tempActiveNub: 0,
      itemNum1: 0,
      itemNum2: 0,
      scrollShow: true,
      tempData: [],
      tempDataLength: 1,
      y: 0,
      pindex: 0,
      categoryArr: [
        { categoryName: "", categoryType: "movie" },
        { categoryName: "", categoryType: "series" },
        { categoryName: "", categoryType: "record" },
        { categoryName: "", categoryType: "sports" },
        { categoryName: "", categoryType: "KX" },
        { categoryName: "", categoryType: "Entertainment" },
        { categoryName: "", categoryType: "JQ" },
        { categoryName: "", categoryType: "MJ" },
        { categoryName: "", categoryType: "ZZ" },
        { categoryName: "", categoryType: "jishi" },
        { categoryName: "", categoryType: "DW" },
        { categoryName: "", categoryType: "shenghuo" },
        { categoryName: "", categoryType: "caijing" },
        { categoryName: "", categoryType: "fazhi" },
        { categoryName: "", categoryType: "shaoer" },
        { categoryName: "", categoryType: "yinye" },
        { categoryName: "", categoryType: "jiaoyu" },
        { categoryName: "", categoryType: "qita" }
      ],
      n: 0,
      tempKeyword: "",
      tempItemNo: 0,
      tempPindex: 0,
      ban: false,
      bstPindex: 0, //
      startBstPsize: 0, //
      tempBstData: [], //
      bstSearch: 0, //
      tempStartBstPsize: 0,
      itemNum:0,//itemNum1+itemNum2
      prep:true,//flase
      tempPindexArr:[0],//pindex
      tempPindexLength:0,
      pageNum:0,//
      clickType:"" // 
    };
  },
  components: {
    kb,Toast
  },
  activated() {
    if (window.localStorage.getItem("categoryArr")) {
      this.categoryArr = JSON.parse(window.localStorage.getItem("categoryArr"));
    } else {
      this.getDetail();
    }
    this.$store.commit("GET_HTTPRESPONSETIME", new Date().getTime());
    this.ban = false;
    console.log("type:" + this.type);
    if (this.type != undefined) {
      // 
      $("#a0 .search_year span b").text("0");
      $("#a1 .search_year span b").text("0");
      $(".returnKey").css("display", "none");
      this.bstPindex = 0, 
      this.startBstPsize = 0, 
      this.tempBstData = [], 
      this.bstSearch = 0, 
      this.tempStartBstPsize = 0,
      this.allShow = true;
      this.firstLoad = true;
      this.isShow = false;
      this.removeCss();
      this.keyword = "";
      this.activeNub = 0;
      (this.searchType = "all"), (this.n = 0), (this.itemNo = -1);
      this.pindex = 0;
      this.objindex = [0, 0];
      this.col = 0;
      this.row = 0;
      this.oldF = [0, 0];
      this.newF = [0, 0];
      $(".search-right .searchImgs").css("left", "0px");
      $(".search-right")
        .stop(false, true)
        .animate({ top: "0px" }, 300);
      console.log("");
      this.$refs.kbc.getKeyListen(0);
      this.$refs.kbc.keyword = "";
      this.$refs.kbc.isDel = false;
      try {
        let overTime = 0;
        if (this.firstLoad) {
          overTime = this.httpResponseTime - this.httpRequestTime;
          console.log("");
          this.firstLoad = false;
        }
        console.log(
          "" +
            overTime +
            "*******" +
            this.firstLoad
        );
        let traceId = "|" + this.catId + "|";
        let traceType = "";
        if (this.type === "index") {
          traceType = "|1|2|";
        } else {
          traceType = "|9|2|";
        }
        bi.loadPage(traceId, traceType, 2, 0, 1, this.catId, "", overTime);
      } catch (error) {
        console.log(error);
      }
    } else {
      console.log("");
    }
  },
  created() {
    this.getCategory();
  },
  mounted() {
  },
  methods: {
    init() {
      this.getDetail();
    },
    // 
    pageLode(Asssetid,asset_name) {
      let SrcPagePath = window.sessionStorage.getItem('srcPagePath');
      let PageName = asset_name;
      let AsssetID = Asssetid;
      let Id = 0;
      let TYPE = "00";
      x.pageLoad(this.clickType,SrcPagePath, PageName, AsssetID, Id, TYPE);
    },
    getDetail() {
      // 
      let that = this;
      if (this.isSouth) {
        south.getSearchRecommend().then(
          data => {
            // 
            let searchList = data.data;
            that.eleList = [];
            if (
              searchList == "" ||
              searchList == null ||
              searchList == undefined
            )
              return;
            if (searchList.hotActorList != undefined) {
              this.recommend = true;
              that.eleList = searchList;
              that.objindex = [0, 0];
            }
            that.$nextTick(function() {
              that.$lazyImages.loadImage();
            });
          },
          response => {
            console.log("");
          }
        );
      } else {
        // 
        api.getSearchRecommend().then(
          data => {
            let searchList = data.data;
            that.eleList = [];
            if (
              searchList == "" ||
              searchList == null ||
              searchList == undefined
            )
              return;
            if (searchList.hotActorList != undefined) {
              this.recommend = true;
              that.eleList = searchList;
              that.objindex = [0, 0];
            }
            that.$nextTick(function() {
              that.$lazyImages.loadImage();
            });
          },
          response => {
            console.log("");
          }
        );
      }
    },
    getCategory() {
      //  
      if (this.isSouth) {
        south
          .getCategory()
          .then(data => {
            // categoryType
            this.reloadTimes = 0;
            let categoryArr = [];
            data.data.categoryList.map(item => {
              let obj = {};
              obj.categoryName = item.categoryName;
              obj.categoryType = item.catCode;
              categoryArr.push(obj);
            });
            this.categoryArr = categoryArr;
            // 
            window.localStorage.setItem(
              "categoryArr",
              JSON.stringify(categoryArr)
            );
          })
          .catch(data => {
            this.reloadTimes++;
            if (this.reloadTimes > 1) {
              console.log(
                "requset search result time out , three times finish"
              );
              return false;
            } else {
              south.getCategory();
            }
          });
      } else {
        api
          .getCategory()
          .then(data => {
            // categoryType
            this.reloadTimes = 0;
            let categoryArr = [];
            data.data.categoryList.map(item => {
              let obj = {};
              obj.categoryName = item.categoryName;
              obj.categoryType = item.catCode;
              obj.categoryId = item.categoryId;
              categoryArr.push(obj);
            });
            this.categoryArr = categoryArr;
            window.localStorage.setItem(
              "categoryArr",
              JSON.stringify(categoryArr)
            );
          })
          .catch(data => {
            this.reloadTimes++;
            if (this.reloadTimes > 1) {
              console.log(
                "requset search result time out , three times finish"
              );
              return false;
            } else {
              api.getCategory();
            }
          });
      }
    },
    // 
    getSearchData() {
      let that = this;
      if (this.isSouth) {
        south.getSearchData(this.keyword, this.searchType).then(
          data => {
            this.reloadTimes = 0;
            try {
              if (data.data[0].resultList == undefined) {
                this.ban = true;
              }
            } catch (error) {
              console.log(error);
            }
            this.assetData = data.data[0].resultList;
            this.tempData = [];
            this.tempDataLength = 1;
            //  
            this.tempData[0] = data.data[0].resultList;
            //  
            this.itemNum1 = data.data[0].resultNum;
            // this.itemNum = this.itemNum1 + this.itemNum2;
            if(this.itemNum1<8){
              this.bstSearch = -1;
            }else{
             this.bstSearch = Math.floor(this.itemNum1 / 8);
            }
            this.startBstPsize = 8 - this.itemNum1 % 8;
            this.tempStartBstPsize = this.startBstPsize;
            this.getBstSearchData();
            that.$nextTick(function() {
              that.$lazyImages.loadImage();
              console.log(new Date());
            });
          },
          response => {
            console.log("");
            this.reloadTimes++;
            if (this.reloadTimes > 1) {
              console.log(
                "requset search result time out , three times finish"
              );
              return false;
            } else {
              south.getSearchData(this.keyword, this.searchType);
            }
          }
        );
      } else {
        api.getSearchData(this.keyword, this.searchType).then(
          data => {
            this.reloadTimes = 0;
            try {
              if (data.data[0].resultList == undefined) {
                this.ban = true;
              }
            } catch (error) {
              console.log(error);
            }
            this.assetData = data.data[0].resultList;
            this.tempData = [];
            this.tempDataLength = 1;
            //  
            this.tempData[0] = data.data[0].resultList;
            //  
            this.itemNum1 = data.data[0].resultNum;
            this.itemNum = this.itemNum1;
            this.pageNum = Math.ceil (this.itemNum/8);
            console.log(""+this.pageNum);
            that.$nextTick(function() {
              that.$lazyImages.loadImage();
              console.log(new Date());
            });
          },
          response => {
            console.log("");
            this.reloadTimes++;
            if (this.reloadTimes > 1) {
              console.log(
                "requset search result time out , three times finish"
              );
              return false;
            } else {
              api.getSearchData(this.keyword, this.searchType);
            }
          }
        );
      }
    },
    // 
    getBstSearchData() {
      let that = this;
      if (this.isSouth) {
        south
          .getBstSearchData(
            this.keyword,
            this.searchType,
            this.bstPindex,
            this.startBstPsize
          )
          .then(data => {
            if (data.data == null) {
              if(this.assetData.length <8){
                this.itemNum = this.assetData.length
              }else{
                this.itemNum = this.itemNum1;
              }
              this.itemNum2 = 0;
               return
            };
            if (this.itemNum1 == 0) {
              if(data.data[0].resultList.length < 8){
                  this.itemNum2 = data.data[0].resultList.length;
                  this.itemNum = this.itemNum1 + this.itemNum2;
