        this.removeCss();
        this.row++;
        if (this.row > 1) this.row = 1;
        this.addCss();
        $("#a" + (this.row - 1) + " .search_year span b").text("0");
      } else {
        if (this.assetData == undefined || this.assetData.length==0) return;
        if (this.navPosition && this.itemNo == -1) {
          this.navPosition = false;
          this.itemNo = 0;
          this.activeNub = 1;
          $("#n" + this.n).addClass("hover");
          $("#n" + this.n).removeClass("focus");
          this.scrollTest();
          return;
        };
        if(!this.isSouth){
          if (this.itemNum1 + this.itemNum2 === this.activeNub ||this.itemNum1 + this.itemNum2 - this.activeNub === 1 ||this.itemNum1 + this.itemNum2 - this.activeNub === 2 ||this.itemNum1 + this.itemNum2 - this.activeNub === 3)return;
        };
        if(this.isSouth){
          if(this.itemNo + 4 > this.assetData.length-1 && this.itemNo <= 3){  
            console.log(''+this.itemNo)     
            return
            };
          if(this.itemNo > 3 && this.assetData.length<8){
              this.itemNum = (this.tempDataLength-1)*8+(this.tempData[this.tempDataLength-1].length);//
              return;
          };
        }
        this.pindex += 4;
        this.activeNub += 4;
        console.log(''+this.assetData.length)
        if (this.itemNo >= 4 && this.assetData.length == 8) {
          if (this.isSouth) {
            console.log('this.y:'+this.y+'this.bstSearch:'+this.bstSearch)
            if (this.y - this.bstSearch >= 0) {
              // this.y+1 -this.bstSearch >= 0
              if(this.startBstPsize == undefined)return
              this.bstPindex = this.startBstPsize;
              this.tempPindexArr.push(this.startBstPsize)
              this.tempPindexLength++;
              // pindexpindex
              this.bstGetNexPage();
              // this.startBstPsize += 8;//8bstPindex
            } else {
              this.getNextPage();
            }
          } else {
            this.getNextPage();
          }
          if (this.activeNub>8) {
            $(".returnKey").css("display", "block");
          }
          return;
        }
        this.itemNo += 4;
        this.scrollTest();
      }
    },250),
    up:lodash.debounce(function()  {
      this.clearTimer();
      if (this.isRecommend) {
        if (this.row == 0) return;
        this.removeCss();
        this.row--;
        this.addCss();
        $("#a" + (this.row + 1) + " .search_year span b").text("0");
      } else {
        if (this.tempDataLength === 1 && this.itemNo <= 3) {
          $("#n" + this.n).addClass("focus");
          $("#n" + this.n).removeClass("hover");
          this.navPosition = true;
          this.itemNo = -1;
          this.activeNub = 0;
          return;
        }
        this.activeNub -= 4;
        this.pindex -= 4;
        if (this.itemNo <= 3) {
          if (this.isSouth) {
            if (this.startBstPsize != this.tempStartBstPsize) {
              // 
              if(this.tempPindexLength == 1){
                console.log('')
                this.startBstPsize = this.tempStartBstPsize;
              }
              this.startBstPsize = this.tempPindexArr[this.tempPindexLength]
              this.tempPindexArr.splice(this.tempPindexLength, 1);
              this.tempPindexLength--;
              // if(this.tempPindexArr[this.tempPindexLength-1] == undefined){
              //   alert('2')
              //   this.startBstPsize = this.tempPindexArr[this.tempDataLength-2]
              //   this.tempPindexArr.splice(this.tempDataLength-1, 2);
              // }else{
              //   // alert('1')
              //   this.startBstPsize = this.tempPindexArr[this.tempDataLength-1]//pindex
              //   this.tempPindexArr.splice(this.tempDataLength-1, 1);//startBstPsize
              // }
            }
          }
          
          this.y--;
          this.tempDataLength--;
          this.assetData = this.tempData[this.tempDataLength - 1];
          this.tempData.splice(this.tempDataLength, 1);
          this.scroll(this.y);
          this.itemNo += 4;
          if (this.activeNub < 8) {
            $(".returnKey").css("display", "none");
          }
          this.$nextTick(function() {
            this.scrollTest();
          });
          return;
        }
        this.itemNo -= 4;
        this.scrollTest();
      }
