    right:lodash.debounce(function(){
      console.log('this.y:'+this.y+'this.bstSearch:'+this.bstSearch)
      if ($("#a" + this.row + " .searchImgs").is(":animated")) return;
      if (this.isRecommend) {
        if (this.row === 0) {
          if (this.col == this.eleList["hotAssetList"].length - 1) return;
        }
        if (this.row === 1) {
          if (this.col == this.eleList["hotActorList"].length - 1) return;
        }
        this.removeCss();
        this.col++;
        this.addCss();
      } else {
        if (this.navPosition && this.itemNo == -1) {
          if ($(".navJ>div:last").hasClass("focus")) return;
          $("#n" + this.n).removeClass("focus");
          this.n++;
          $("#n" + this.n).addClass("focus");
          if (this.n >= 8) {
            $(".navJ").css({
              transition: "all 0.3s",
              transform: "translateX(-" + 90 * (this.n - 7) + "px)"
            });
          }
          this.searchType = $("#n" + this.n)
            .text()
            .replace(/[^a-zA-Z]/g, "");
          this.itemNum1 = 0;
          this.itemNum2 = 0;
          this.bstPindex = 0;
          this.tempBstData = [];
          this.y = 0;
          // this.activeNub = 1;
          this.getSearchData();
          return;
        }
        if ((this.itemNo + 1) % 4 === 0) return;
        if(this.itemNo+1 >= this.assetData.length)return;
        if (this.activeNub >= this.itemNum1 + this.itemNum2) return;
        this.clearTimer();
        this.activeNub++;
        this.itemNo++;
        this.scrollTest();
      }
    },250) ,
    addCss() {
      this.newF = [this.row, this.col];
      if (this.objindex[this.oldF[0]] != this.objindex[this.newF[0]]) {
        if (this.oldF[1] > this.newF[1]) {
          this.col =
            this.col -
            (this.objindex[this.oldF[0]] - this.objindex[this.newF[0]]) +
            (this.oldF[1] - this.newF[1]);
        } else {
          if(this.row == 1){
            if(this.col - (this.objindex[this.oldF[0]] - this.objindex[this.newF[0]]) >= this.eleList["hotActorList"].length){
              this.col = this.eleList["hotActorList"].length-1;
            }else{
               this.col = this.col - (this.objindex[this.oldF[0]] - this.objindex[this.newF[0]]);
            }
          }else{
            if(this.col - (this.objindex[this.oldF[0]] - this.objindex[this.newF[0]]) >= this.eleList["hotAssetList"].length){
              this.col = this.eleList["hotAssetList"].length-1;
            }else{
               this.col = this.col - (this.objindex[this.oldF[0]] - this.objindex[this.newF[0]]);
            }
          }         
        }
      }
      let obj = $("#a" + this.row + " #b" + this.col);
      $("#a" + this.row + " .search_year span b").text(this.col + 1);
      if (this.allShow) {
        let left = $(obj).offset().left + 175;
        if (this.row >= 1) {
          $(".search-right")
            .stop(false, true)
            .animate({ top: -350 * (this.row - 1) + "px" }, 300);
        }
        let $left = 0;
        if (left > 1280) {
          // 1280
          if (this.isRecommend) {
            // 
            if (this.row == 0) {
              $("#a" + this.row + " .searchImgs")
                .stop(false, true)
                .animate({ left: "-=205px" }, 300);
            } else {
              $("#a" + this.row + " .searchImgs")
                .stop(false, true)
                .animate({ left: "-=202px" }, 300);
            }
          } else {
            if (
              this.row == 0 &&
              this.eleList[this.row].categoryType == "actor"
            ) {
              $("#a" + this.row + " .searchImgs")
                .stop(false, true)
                .animate({ left: "-=205px" }, 300);
            } else {
              $("#a" + this.row + " .searchImgs")
                .stop(false, true)
                .animate({ left: "-=202px" }, 300);
            }
          }
          this.objindex[this.row]++;
        } else if (left < 570) {
          if (this.isRecommend) {
            // 
            if (this.row == 0) {
              $("#a" + this.row + " .searchImgs")
                .stop(false, true)
                .animate({ left: "+=205px" }, 300);
            } else {
              $("#a" + this.row + " .searchImgs")
                .stop(false, true)
                .animate({ left: "+=202px" }, 300);
            }
          } else {
          }

          this.objindex[this.row]--;
        }
      }
      obj.addClass("change");
    },
    clearTimer() {
      // 
      if (this.timer !== null) {
        clearInterval(this.timer);
        let tab = $("#n" + this.itemNo + " .listname")[0];
        tab.scrollLeft = 0;
        tab.style.textOverflow = "ellipsis";
        $("#n" + this.itemNo + " .listname .bJ")[0].innerHTML = "";
        this.timer = null;
      }
    },
    removeCss() {
      this.oldF = [this.row, this.col];
      let obj = $("#a" + this.row + " #b" + this.col);
      obj.removeClass("change");
      if (this.timer != null) {
        clearInterval(this.timer);
        // this.timer = null;
        // let tab = obj.next()[0];
        // tab.scrollLeft = 0;
        // tab.style.textOverflow = "ellipsis";
      }
    },
    Marquee(tab, tab1, tab2) {
      if (tab2.offsetWidth - tab.scrollLeft <= 0) {
        // tab.scrollLeft -= tab1.offsetWidth;
        tab.scrollLeft = 0; // 
      } else {
        tab.scrollLeft++;
      }
    },
    scrollTest() {
      let p_title = $("#n" + this.itemNo + " .listname .aJ").text();
      console.log("-----" + p_title);
      if (p_title.length > 10) {
        console.log("");
        console.log("" + p_title.length);
        let speed = 40;
        let that = this;
        let tab = $("#n" + this.itemNo + " .listname")[0];
        let tab1 = $("#n" + this.itemNo + " .listname .aJ")[0];
        let tab2 = $("#n" + this.itemNo + " .listname .bJ")[0];
        tab.style.textOverflow = "clip";
        tab2.innerHTML = tab1.innerHTML;
        clearInterval(this.timer);
        this.timer = null;
        this.timer = setInterval(function() {
          that.Marquee(tab, tab1, tab2);
        }, speed);
      }
    },
    keepDo(bl, name, pos) {
      if (bl) {
        this.keyword = name;
      } else {
        if (pos === "top") {
          this.navPosition = true;
          this.isShow = true;
          this.row = -1;
          this.n = this.activeN;
          $("#n" + this.n).addClass("focus");
          $("#n" + this.n).removeClass("hover");
        }
        if (pos === "left" && name === "asset") {
          this.$refs.kbc.getKeyListen(5);
          this.$refs.kbc.getType("asset");
        }
        if (pos === "left" && name === "actor") {
          this.$refs.kbc.getKeyListen(5);
          this.$refs.kbc.getType("actor");
        }
        if (pos == "right" || pos == "search") {
          if (this.isRecommend) {
            this.isShow = true;
            this.row = this.oldF[0];
            this.col = this.oldF[1];
            this.addCss();
            try {
              // 
              let traceId = c.getTrackPath();
              let traceType = "";
              if (this.type === "index") {
                traceType = "|1|2|";
              } else {
                traceType = "|9|2|";
              }
              bi.searchResultClick(
                this.keyword,
                1,
                traceId,
                traceType,
                1,
                this.catId,
                2,
                0
              );
            } catch (error) {
              console.log(error);
            }
          } else {
            if (this.tempKeyword == this.keyword) {
              this.activeNub = this.tempActiveNub;
              this.itemNo = this.tempItemNo;
              this.pindex = this.tempPindex;
              this.scrollTest();
              // 
            } else {
              this.activeNub = 1;
              this.itemNo = 0;
              this.scrollTest();
              // 
            }
            this.isShow = true;
          }
        }
      }
    },
    // 
    getNextPage() {
      if (this.isSouth) {
        south
          .getSearchItmeNextPageData(this.keyword, this.searchType, this.pindex)
          .then(jsonData => {
            if(jsonData.data[0].resultList.length<=0){
              return;
            }
            this.assetData = [];
            this.assetData = jsonData.data[0].resultList;
            if (this.bstSearch == this.y+1 && this.tempBstData.length > 0 && (jsonData.data[0].resultList.length + this.tempBstData.length) <= 8) {
              console.log('1:'+this.bstSearch == this.y +'2:'+this.tempBstData.length > 0+'3:'+(jsonData.data[0].resultList.length + this.tempBstData.length) <= 8)
              if (this.assetData == undefined) {
                this.assetData = [];
              }
              for (let i = 0; i < this.tempBstData.length; i++) {
                const element = this.tempBstData[i];
                this.assetData.push(element);
              }
            }
            this.tempData.push(jsonData.data[0].resultList);
            this.tempDataLength++;
            this.$nextTick(function() {
              this.$lazyImages.loadImage();
              this.y++;
              this.scroll(this.y);
              if(jsonData.data[0].resultList.length <= (this.itemNo - 4)){
                this.itemNo = 0;
              }else{
                this.itemNo -= 4;
                if(this.itemNo<0){
                  this.itemNo = 0;
                }
              }
              this.scrollTest();
            });
          });
      } else {
        api
          .getSearchItmeNextPageData(this.keyword, this.searchType, this.pindex)
          .then(jsonData => {
            if(jsonData.data[0].resultList.length<=0){
              return;
            }
            this.assetData = jsonData.data[0].resultList;
            this.tempData.push(jsonData.data[0].resultList);
            this.tempDataLength++;
            this.$nextTick(function() {
              this.$lazyImages.loadImage();
              this.y++;
              this.scroll(this.y);
              if(jsonData.data[0].resultList.length <= (this.itemNo - 4)){
                this.itemNo = 0;
              }else{
                this.itemNo -= 4;
                if(this.itemNo<0){
                  this.itemNo = 0;
                }
              }
              this.scrollTest();
            });
          });
      }
    },
    // 
    bstGetNexPage() {
      if (this.isSouth) {
        south
          .getBstSearchData(this.keyword, this.searchType, this.bstPindex, 8)
          .then(jsonData => {
            if(jsonData.data[0].resultList.length<=0){
              return;
            }
            this.assetData = jsonData.data[0].resultList;
            this.tempData.push(jsonData.data[0].resultList);
            this.tempDataLength++;
            if(jsonData.data[0].index == undefined)return;
            this.startBstPsize = jsonData.data[0].index*1;
            this.$nextTick(function() {
              this.$lazyImages.loadImage();
              this.y++;
              this.scroll(this.y);
              if(jsonData.data[0].resultList.length <= (this.itemNo - 4)){
                this.itemNo = 0;
              }else{
                this.itemNo -= 4;
                if(this.itemNo<0){
                  this.itemNo = 0;
                }
              }
              this.scrollTest();
            });
          });
      }
    },
    scroll(y) {
      if (this.itemNum1 + this.itemNum2 >= 8) {
        let ratio = 8 / (this.itemNum1 + this.itemNum2);
        let distance = 349 * ratio;
        $(".scroll-bar")
          .stop(false, true)
          .animate({
            top: distance * y <= 0 ? "-1" : distance * y + "px"
          });
      }
    }
  },
  watch: {
    keyword() {
      if(this.keyword.length === 10){
        Toast("")
      }
      this.pindex = 0;
      if (this.keyword.length == 0) {
        // 
        this.pageNum = 0
        this.getDetail();
        this.isRecommend = true;
        this.navShow = false;
        this.assetData = [];
        this.tempData = [];
        this.tempDataLength = 1;
        this.tempPindexArr = [0];
        this.tempPindex = 0;
        this.tempPindexLength = 0;
        this.searchType = 'all';
        this.n = 0;
      } else {
        this.y = 0;
        this.scroll(this.y);
        this.getSearchData();
        this.isRecommend = false;
      }
      this.objindex = [];
      this.col = 0;
      this.row = 0;
      this.oldF = [0, 0];
      this.newF = [0, 0];
      $(".search-right .searchImgs").css("left", "0px");
      $(".search-right")
        .stop(false, true)
        .animate({ top: "0px" }, 300);
    },
    eleList() {
      let opoo = "";
      for (let i = 0; i < this.eleList.length; i++) {
        const element = this.eleList[i];
        opoo += element.categoryName;
      }
      if (this.eleList.length == 0) {
        // 
        this.navShow = false;
        try {
          // 
          let traceId = c.getTrackPath();
          let traceType = "";
          if (this.type === "index") {
            traceType = "|1|2|";
          } else {
            traceType = "|9|2|";
          }
          bi.searchResultClick(
            this.keyword,
            2,
            traceId,
            traceType,
            1,
            this.catId,
            2,
            0
          );
        } catch (error) {
          console.log(error);
        }
      }
      this.$nextTick(function() {
        this.$store.commit("GET_HTTPRESPONSETIME", new Date().getTime());
        let overTime = 0;
        if (this.firstLoad) {
          overTime = this.httpResponseTime - this.httpRequestTime;
          console.log("");
          this.firstLoad = false;
        } else {
          return "";
        }
        //  if(this.catCode==undefined)this.catCode=0;
        //  if(this.catId==undefined)this.catId=0;
        let traceId = c.getTrackPath();
        let traceType = "";
        if (this.type === "index") {
          traceType = "|1|2|";
        } else {
          traceType = "|9|2|";
        }
        bi.loadPage(traceId, traceType, 2, 0, 1, this.catId, "", overTime);
      });
    },
    assetData() {
      try {
        if(this.assetData !== undefined){
          if(this.assetData.length == 0){
            this.y = -1;
          }
          if (this.assetData.length > 0) {
            this.isRecommend = false;
            this.navShow = true;
            this.ban = false;
            
          } else {
            this.ban = true;
            // this.isRecommend = true;
            // this.navShow =false;
            $(".returnKey").css("display", "none");
          };
        }
        if (this.isRecommend) {
          this.ban = false;
        }
      } catch (error) {
        console.log(error);
      }
    },
  },
  computed: {
    ...mapGetters([
      "rootPath",
      "catList",
      "category",
      "behindParams",
      "isHTTPS",
      "httpRequestTime",
      "httpResponseTime",
      "isSouth"
    ])
  },
  beforeRouteLeave(to, from, next) {
    try {
      let overTime = 0;
      overTime = new Date().getTime() - this.httpResponseTime;
      //  if(this.catCode==undefined)this.catCode=0;
      //  if(this.catId==undefined)this.catId=0;
      let traceId = c.getTrackPath();
      let traceType = "";
      if (this.type === "index") {
        traceType = "|1|2|";
      } else {
        traceType = "|9|2|";
      }
      bi.closPage(traceId, traceType, 2, 0, "", overTime);
      clearInterval(this.timer);
      this.timer = null;
      console.log("" + overTime + "^^^^^^^focusId:" + this.catId);
    } catch (error) {
      console.log(error);
    } finally {
      next();
    }
  },
  beforeRouteEnter (to, from, next) {
    window.sessionStorage.removeItem('srcPagePath');
    window.sessionStorage.setItem('srcPagePath',location.origin+'/henan'+from.path);
    next()
  },
};
</script>



