<style>
  .HistoryImg {
    width: 100%;
    height: 100%;
    border-radius: 15px;
    box-sizing: border-box;
  }

  .Img {
    width: 125px;
    height: 125px;
    border-radius: 50%;
  }

</style>
<template>
  <div class="his_right">
    <div class="btn_rem">{{msg}}</div>
    <div class="btn_num">
      <span v-if="Tvnum[0]>0">{{Tvnum[0]}}</span>
      <span v-if="Tvnum[1]>0">{{Tvnum[1]}}</span>
      <span v-if="Tvnum[2]>0">{{Tvnum[2]}}</span>
      <span v-if="Tvnum[3]>0">{{Tvnum[3]}}</span>
      <span v-if="Tvnum[4]>0">{{Tvnum[4]}}</span>
    </div>
    <div class="btn_center">
      <ul class="his_ul" v-if="show">
        <li v-for="(item,index) in jsonUrl" :key="index" :id="('Img'+index)" class="imgs">
          <lazy-image :src='item.assetImg' :placeholder='placeholder' :initScore='initScore' :score="item.score" :title="item.assetName"
            :imgClass='HistoryImg' />
        </li>
      </ul>
      <ul class="his_ul" v-if="!show">
        <li v-for="(item,index) in jsonUrl" :key="item.id" :id="('Img'+index)" class="imgss">
          <lazy-image :src='item.headImg' :placeholder='placeholder' :initScore='initScore' :imgClass='Img' />
          <p>{{item.assetName}}</p>
          <h3 class='collectActor'>{{item.assetName.substring(0,1)}}</h3>
        </li>
      </ul>
    </div>
    <div class="gunLun" v-if="this.jsonUrl.length>8">
      <div class="hang" v-if="!numop">{{op+1}}/{{Math.ceil(jsonUrl.length/4)}}</div>
      <div class="hang" v-if="numop">1/{{Math.ceil(jsonUrl.length/4)}}</div>
      <div class="btn_nums"></div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
  </div>
</template>
<style scoped="hCenterssss">
  .gunLun {
    width: 6px;
    height: 490px;
    background: rgba(255, 255, 255, 0.4);
    border-radius: 5px;
    margin-top: 45px;
    position: absolute;
    left: 1250px;
    top: 100px;
  }
  .hang {
    position: absolute;
    left: -8px;
    top: -36px;
  }

  .gunLun .btn_nums {
    width: 10px;
    height: 140px;
    background: white;
    border-radius: 5px;
    text-align: center;
    position: absolute;
    left: -2px;
    top: -10px;
    color: black;
  }

  .removeBorder {
    border: 3px solid white;
    box-sizing: border-box;
    line-height: 42px;
  }

  .btn_center {
    width: 780px;
    height: 550px;
    overflow: hidden;
  }

  .imgs.box_border {
    border: 3px solid #ffffff;
    box-sizing: border-box;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);
  }

  .his_right {
    width: 840px;
    height: 720px;
    overflow: hidden;
    float: left;
    margin-left: 104px;
    margin-top: 25px;
  }

  .btn_rem {
    width: 138px;
    height: 50px;
    background: #F1E00E;
    color: black;
    font-size: 20px;
    text-align: center;
    line-height: 50px;
    border-radius: 15px;
    float: left;

  }
  .collectActor{
    position: relative;
    width:80px;
    height:80px;
    display: inline-block;
    top: -145px;
    left: 40px;
    font-size: 45px;
    font-style: normal;
    font-weight: 300;
  }
  .btn_num {
    float: left;
    width: 700px;
    height: 28px;
    overflow: hidden;
    margin-top: 10px;
    margin-bottom: 54px;

  }

  .btn_num span {
    color: #B0D1F7;
    font-size: 20px;
    margin-left: 24px;
    height: 24px;

  }

  .his_right ul {
    float: left;
  }

  .imgs {
    width: 161px;
    height: 230px;
    margin-bottom: 45px;
    margin-right: 32px;
    float: left;
    border-radius: 15px;
  }

  .imgss {
    width: 125px;
    height: 125px;
    float: left;
    margin: 40px 60px 110px 0;
    border-radius: 50%;
  }

  .imgss.box_border {
    border: 3px solid #ffffff;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);
  }

  .his_ul {
    margin-left: 8px;
    display: -webkit-flex;
    /* Safari */
    display: flex;
    flex-wrap: wrap;
  }

  .imgss p {
    max-width: 125px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: center;
    width: 100%;
    height: 20px;
  }

  .sprite_img {
    width: 35px;
    height: 35px;
    padding: 5px 2.5px 7px 35px;
    background: url("../../assets/icon.png") no-repeat -35px -245px;
  }

</style>
<script>
  import {
    mapGetters
  } from 'vuex'
  import {
    HTTPUtil as api
  } from '../../fetch/api.js'

  import {
    bp as bi
  } from '../../js/ga.js'
  import c from '../../js/common.js'
  import { xha } from "../../js/xha.js";
  export default {
    props: ['jsonUrl', 'Tvnum','bp','acc'],
    data() {
      return {
        msg: '',
        num: 1,
        itemNo: 0,
        op: 0,
        show: true,
        HistoryImg: 'HistoryImg',
        Img: 'Img',
        placeholder: require('../../assets/img_loading_160x230.png'),
        initScore: require('../../assets/corner_mark.png'),
        isShow: false,
        addRemver: 0,
        numop:false,
        prevPathName:null
      }
    },
    activated() {
       if(this.bp!=undefined){
         $('.btn_rem').removeClass('removeBorder');
         this.removeCss();
         this.isShow = false;
         this.op = 0;
         $('.his_ul').css({'transform': 'translateY(-' + 275 * this.op + 'px)'});
      }
    },updated() {
      this.$nextTick(function() {
        this.$lazyImages.loadImage();
       })
    },
    methods: {
      keyCode(kc) {
        if (kc == "KeyBack") {
          this.$emit('Center', 'back');
        } else if (kc == "right") {
          this.right();
        } else if (kc == "left") {
          this.left();
        } else if (kc == "down") {
          this.down();
        } else if (kc == "up") {
          this.up();
        } else if (kc == "KeyEnter") {
          this.KeyEnter();
        }
      },
      move() {
        let number = Math.ceil(this.jsonUrl.length / 4);
        let ly = Math.ceil(490 / number);
        $('.btn_nums').css({
          'transition': 'all 0.4s',
          'transform': 'translateY(' + ly * this.op + 'px)'
        });
      },
      addCss() {
        $('#Img' + this.itemNo).addClass('box_border');
      },
      removeCss() {
        $('#Img' + this.itemNo).removeClass('box_border');
      },
      down() {
        this.numop = false;
        if (this.op < 0) {
          this.op = 0;
          $('.btn_rem').removeClass('removeBorder');
        } else
        if (this.op < Math.ceil(this.jsonUrl.length / 4) - 1) {
          this.removeCss();
          this.itemNo += 4;
          if (this.itemNo > this.jsonUrl.length - 2) {
            this.itemNo = this.jsonUrl.length - 1;
          }
          let top = this.cEleOffsetTop('#Img' + this.itemNo);
          if (top > 300) {
            this.op++;
            $('.his_ul').css({
              'transition': 'all 0.3s',
              'transform': 'translateY(-' + 275 * this.op + 'px)'
            });
            this.move();
          }
        }
        this.addCss();
      },
      up() {
        if (this.op <= 0) {
          this.op = -1;
          this.numop = true;
          $('.btn_rem').addClass('removeBorder');
          this.removeCss();
        } else {
          this.numop = false;
          this.removeCss();
          this.itemNo -= 4;
          let top = this.cEleOffsetTop('#Img' + this.itemNo);
          if (top < 260) {
            this.op--;
            $('.his_ul').css({
              'transition': 'all 0.3s',
              'transform': 'translateY(-' + 275 * this.op + 'px)'
            });
            this.move();
          }
          this.addCss();
        }
      },
      right() {
        if (this.op < 0) return;
        for (let i = 0; i < Math.ceil(this.jsonUrl.length / 4); i++) {
          if (this.itemNo == 3 || this.itemNo == 3 + (4 * i) || this.itemNo == this.jsonUrl.length - 1) return;
        }
        this.removeCss();
        this.itemNo++;
        this.addCss();
      },
      left() {
        for (let i = 0; i < Math.ceil(this.jsonUrl.length / 4); i++) {
          if (this.itemNo <= 0 || this.itemNo == (0 + (4 * i)) || this.op == -1) {
            $('.his_ul').css({'transform': 'translateY(-0px)'});
            this.isShow = false;
            this.removeCss();
            this.itemNo = 0;
            this.$emit('Center', 'left');
          }
        }
        if (this.jsonUrl.length == 0) {
          $('.his_ul').css({'transform': 'translateY(-0px)'});
          this.$emit('Center', 'left');
          $('.btn_rem').removeClass('removeBorder');
          this.isShow = false;
        } else {
          this.removeCss();
          $('.btn_rem').removeClass('removeBorder');
          this.itemNo--;
          this.addCss();
        }
      },
      pageLoad(assetId,assetName) {
      let SrcPagePath = "";
      let PageName = '';
      let AsssetID = assetId;
      let Id = 0;
      let TYPE = '0'+(this.navpos+1);
      if (this.acc) {
        PageName = ''
      } else {
        PageName = ''
      }
      xha.pageLoad(PageName,SrcPagePath, assetName, AsssetID, Id, TYPE);
    },
      KeyEnter() {
        // this.isShow = false;
        if (this.op < 0) { // 
          $('.btn_rem').removeClass('removeBorder');
          this.$emit("Center", 'KeyEnter','',this.show, 'clear');
        } else { // 
        if(this.acc){
          // 
          window.localStorage.setItem('EnterType','4');        
        }else{
          // 
          window.localStorage.setItem('EnterType','3');
        }
          try {
            this.pageLoad(this.jsonUrl[this.itemNo].assetId,this.jsonUrl[this.itemNo].assetName);
          } catch (error) {
            console.log(error);
          }
          let url = this.jsonUrl[this.itemNo];
          if(url.assetType=="Series"||"Movie"==url.assetType||url.assetType=="Column"){
            c.routerSkip(url.layoutUrl?url.layoutUrl:url.jsonUrl,"1",{},this.$router);
          }else if(url.assetType==""){
             c.routerSkip(url.layoutUrl?url.layoutUrl:url.jsonUrl,"2",{},this.$router);
          }else if(url.assetType==""){
             c.routerSkip(url.layoutUrl?url.layoutUrl:url.jsonUrl,"5",{},this.$router);
          }else if(url.assetType==""){
             c.routerSkip(url.layoutUrl?url.layoutUrl:url.jsonUrl,"4",{},this.$router);
          }
          // this.$emit("Center", 'KeyEnter', url, this.show,);
        }
      },
      showact() {
        //          
        if (this.jsonUrl.length != 0) {
          try {
            if (this.jsonUrl[0].assetType == '') {
              this.show = false;
            } else {
              this.show = true;
            }
          } catch (error) {
            console.log(error);
          }
        }
      },updateStast(pos) {
        console.log(pos);
        this.isShow = true;
        if (pos == 'right') {
          this.itemNo = 0;
          this.op = 0;
          $('.his_ul').css({'transition': 'all 0.3s','transform': 'translateY(-' + 275 * this.op + 'px)'});
          this.addCss();
        }
        if (pos == 'remove' || pos == 'back') {
          this.removeCss();
          this.itemNo = 0;
          this.op = 0;
          this.isShow = true;
          this.addCss();
          this.$emit("Center", 'json');
        }
      },
      cEleOffsetTop(ele) {
        let fEleDistance = $(ele).offset();
        let realHeight = fEleDistance.top + 275;
        return realHeight;
      },
    },
    watch: {
      jsonUrl() {
        this.showact();
        console.log("update");
        if(this.itemNo>(this.jsonUrl.length-1)){// 
          const temItemNo = this.itemNo-1;
          if(temItemNo<0){
            this.isShow = false;
            this.removeCss();
            $('.his_ul').css({'transform': 'translateY(-0px)'});
            this.$emit('Center', 'left');
            this.itemNo = 0;
          }else{
            this.itemNo = temItemNo;
            this.$nextTick(function(){
              this.addCss();
            });
          }
        }        
      }
    },
    computed: {
      ...mapGetters([
        'isSouth',
        'navpos'
      ])
    },
    components: {

    }
  }

</script>



