<!--  -->
<template>
  <div class="filterBox">
      <!--  -->
      <div class="filterType hover" style="width:85px">
          <ul style="width:85px">
              <template v-for="(item,index) in dimensionMap.contentList"  >
                <li :key="'type'+index" v-if="item.categoryName.length>=5 && index == 3 && col == 0"><marquee height="52px" scrollamount="2" behaviour="Scroll" direction="left">{{item.categoryName}}</marquee></li>
                <li :key="'type'+index" v-else >{{item.categoryName}}</li>
              </template>
          </ul>
      </div>
      <!--  -->
      <!-- <div class="filterLocal">
          <ul>
              <template v-for="(item,index) in dimensionMap.areaList">
                <li>{{item.categoryName}}</li>
              </template>
          </ul>
      </div>    -->
      <!--  -->
      <div class="filterYear" style="width:85px;margin-left: 3px;">
          <ul>
              <template v-for="(item,index) in dimensionMap.yearsList">
                <li :key="'year'+index">{{item.categoryName}}</li>
              </template>
          </ul>
      </div>
      <!--  -->
      <div class="filterSort" style="width:85px">
          <ul>
              <template v-for="(item,index) in dimensionMap.sortList">
                <li :key="'sort'+index">{{item.categoryName}}</li>
              </template>
          </ul>
      </div>
      <!--  -->
      <keyDo ref="keydo" v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>
<style>
    .filterBox{
        position:relative;
        box-sizing: border-box;
        height:423px;
    }
    .filterType,.filterLocal,.filterYear,.filterSort{
        position: relative;
        display: block;
        height: 100%;
        float: left;
        overflow: hidden;
        margin-left: 1px;
        background: linear-gradient(to bottom,rgba(162,143, 113,0) 0%,rgba(255,255,255,0.4),rgba(162,143, 113,0) 100%);
    }
    .filterType,.filterLocal{
        width: 70px;
    }
    .filterYear,.filterSort{
        width: 70px;
    }
    
    .filterLocal {
        margin-left: 3px;
        margin-right: 3px;
    }

    .filterSort {
        margin-left: 3px;
    }
    .filterType li,.filterLocal li,.filterYear li,.filterSort li{
        height:52px;
        line-height: 52px;
        text-align: center;
        overflow: hidden;/**/
        text-overflow:ellipsis;/*  */
        white-space: nowrap;/* */
    }
    .filterBox li:nth-child(4){
        background-color: rgba(255, 255, 255, 0.2);
        width: 100%;
        height: 52px;
        border-radius: 11px;
    }
    .filterType ul.hover,.filterLocal ul.hover,.filterYear ul.hover,.filterSort ul.hover{
        /* border: 1px solid rgba(255, 255, 255, 1);
        background-color: rgba(255, 255, 255, 0.3);
        border-radius: 15px;
        height: 421px;
        overflow: hidden; */
    }
    .filterBox div.hover{
