        background-color: rgba(255, 255, 255, 0.3);
        border-radius: 15px;
        height: 421px;
        overflow: hidden;
    }
    .filterType li.hover,.filterLocal li.hover,.filterYear li.hover,.filterSort li.hover{
        background-color: #1f8eaf !important;
        border: 3px solid #ffffff;
        border-radius: 13px;
