                  if(this.specialCode == 'YPYS'){
                      console.warn('')
                       this.dimensionMap.contentList = JSON.parse(JSON.stringify(this.ypysCategoryArr));
                   }else{ 
                      const oneCategory = localStorage.getItem('categoryArr1');
                      if(oneCategory!==null&&oneCategory!==undefined&&oneCategory!==""){
                          console.log("");
                          this.dimensionMap.contentList = JSON.parse(oneCategory);
                      }else{
                          this.dimensionMap.contentList = JSON.parse(JSON.stringify(jsonData.data.categoryList)); 
                          this.categoryArr =   JSON.parse(JSON.stringify(jsonData.data.categoryList));
                      }
                   }
               }else{
                    this.dimensionMap.contentList.unshift({categoryName:"",categoryId:"",catCode:""});
               }
               this.dimensionMap.contentList.unshift({categoryName:"",categoryId:"",catCode:""});
               this.dimensionMap.contentList.unshift({categoryName:"",categoryId:"",catCode:""});
               this.dimensionMap.contentList.unshift({categoryName:"",categoryId:"",catCode:""});

               this.dimensionMap.areaList.unshift({categoryName:"",categoryId:"",catCode:""});
               this.dimensionMap.areaList.unshift({categoryName:"",categoryId:"",catCode:""});
               this.dimensionMap.areaList.unshift({categoryName:"",categoryId:"",catCode:""});
               this.dimensionMap.areaList.unshift({categoryName:"",categoryId:"",catCode:""});

               this.dimensionMap.sortList.unshift({categoryName:"",categoryId:"",catCode:""});
               this.dimensionMap.sortList.unshift({categoryName:"",categoryId:"",catCode:""});
               this.dimensionMap.sortList.unshift({categoryName:"",categoryId:"",catCode:""});
               this.dimensionMap.sortList.unshift({categoryName:"",categoryId:"",catCode:""});

               this.dimensionMap.yearsList.unshift({categoryName:"",categoryId:"",catCode:""});
               this.dimensionMap.yearsList.unshift({categoryName:"",categoryId:"",catCode:""});
               this.dimensionMap.yearsList.unshift({categoryName:"",categoryId:"",catCode:""});
               this.dimensionMap.yearsList.unshift({categoryName:"",categoryId:"",catCode:""});
            //    this.dimensionMap.sortList[this.dimensionMap.sortList.length-1].categoryName = ''
               let xh = 0;
                for (let index = 0; index <  this.dimensionMap.contentList.length; index++) {
                    const element =  this.dimensionMap.contentList[index];
                    if(element.categoryName==this.categoryName){// 
                        xh = index;
                        break;
                    }else if(this.categoryName==""&&(element.categoryName==""||element.categoryName=="")){
                        xh = index;
                        break;
                    }
                }
               
                xh = xh - 3;
                let delArr = this.dimensionMap.contentList.splice(0,xh);
                this.dimensionMap.contentList = this.dimensionMap.contentList.concat(delArr);
                this.$nextTick(function(){
                    this.gather ++;
                    this.addCss();
                });
            }
      },
      sortDefault(){
        if(this.col  == 0){
            if($(".filterType li").eq(3).text()==""){
                this.dimensionMap.contentList[3].categoryName = '';
            }else if( $(".filterType li").eq(3).text()==""){
                this.dimensionMap.contentList[3].categoryName = '';
            }
        }else if(this.col  == 1){
            if($(".filterLocal li").eq(3).text()==""){
                this.dimensionMap.areaList[3].categoryName = '';
            }else if($(".filterLocal li").eq(3).text()==""){
                this.dimensionMap.areaList[3].categoryName = '';
            }
        }else if(this.col  == 2){
             if($(".filterYear li").eq(3).text()==""){
                this.dimensionMap.yearsList[3].categoryName = '';
            }else if( $(".filterYear li").eq(3).text()==""){
                this.dimensionMap.yearsList[3].categoryName = '';
            }
        }else if(this.col  == 3){
            if($(".filterSort li").eq(3).text()==""){
                this.dimensionMap.sortList[3].categoryName = '';
            }else if( $(".filterSort li").eq(3).text()==""){
                this.dimensionMap.sortList[3].categoryName = '';
            }
        }
      },
      keyCode(kc){
         if (kc == "down") {
          this.down();
          this.gather++;
        } else if (kc == "up") {
          this.up();
          this.gather++;
        } else if (kc == "left") {
          this.left();
          this.gather++;
        } else if (kc == "right") {
          this.right();
          this.gather++;
        } else if (kc == "KeyEnter") {
          this.KeyEnter();
        } else if (kc == "KeyBack") {
          this.isShow = false;
          console.log("");
          this.$emit('kp',this.isShow,'goback');
        }
        this.addCss();
      },left(){
          if(this.col==0)return;
          if(this.col==2){
                this.removeCss();
                this.sortDefault();
                this.col=0;
                this.addCss();
                this.sortDefault();
          }else{
                this.removeCss();
                this.sortDefault();
                this.col--;
                this.addCss();
                this.sortDefault();
          }
      },right(){
           if(this.col==3){
                if(!this.isRight){
                    this.removeCss();
                    this.isShow = false;
                    this.$emit('kp',this.isShow,'btnClick');
                    return;
                }
                    this.removeCss();
                    this.isShow = false;
                    this.$emit('keepDo',this.isShow);
                    this.rightMove()
                    return;
          };
          this.removeCss();
          if(this.col==0){// 2018.6.29
              this.sortDefault();
              this.col=2;
              this.addCss();
              this.sortDefault();
          }else{ 
              this.sortDefault();
              this.col++;
              this.addCss();
              this.sortDefault();
          }
          
      },down(){
          this.TurnDown();
      },up(){
          this.TurnUp();
      },addCss(){
            if(this.col == 0){
                    $('.filterType').addClass('hover');
                    $('.filterType li').eq(3).addClass('hover');
            }else if(this.col == 1){
                    $('.filterLocal').addClass('hover');
                    $('.filterLocal li').eq(3).addClass('hover');
            }else if(this.col == 2){
                    $('.filterYear').addClass('hover');
                    $('.filterYear li').eq(3).addClass('hover');
            }else if(this.col == 3){
                    $('.filterSort').addClass('hover');
                    $('.filterSort li').eq(3).addClass('hover');
            }
      },removeCss(){
            if(this.col == 0){
                    $('.filterType').removeClass('hover');
                    $('.filterType li').eq(3).removeClass('hover');
            }else if(this.col == 1){
                    $('.filterLocal').removeClass('hover');
                    $('.filterLocal li').eq(3).removeClass('hover');
            }else if(this.col == 2){
                    $('.filterYear').removeClass('hover');
                    $('.filterYear li').eq(3).removeClass('hover');
            }else if(this.col == 3){
                    $('.filterSort').removeClass('hover');
                    $('.filterSort li').eq(3).removeClass('hover');
            }
      },TurnDown(){
          if(this.col==0){
             try{
                if(this.dimensionMap.contentList[5].categoryName==""){
                 $('.filterType ul li').eq(7).css('opacity','0')
                }
             }catch(error){
                 console.log(error)
             }
              if(this.dimensionMap.contentList[4].categoryName=="")return;
              let t1 =  this.dimensionMap.contentList.shift();
              this.dimensionMap.contentList.push(t1);
              if(this.categoryName == '' || localStorage.getItem('filtroTitleType')===""){
                for (let index = 0; index < this.dimensionMap.contentList.length; index++) {
                  const element = this.dimensionMap.contentList[index];
                  if(element.categoryName == $(".filterType li").eq(3).text()){
                    // this.dimensionMap.sortList.categoryId = this.dimensionMap.contentList[index+1].categoryId;
                    this.getYearsList(element.categoryName,'down');
                  }
              }
              }
          }else if(this.col==1){
              try{
                if(this.dimensionMap.areaList[5].categoryName==""){
                    $('.filterLocal ul li').eq(7).css('opacity','0')
                }
              }catch(error){
                  console.log(error)
              }
              if(this.dimensionMap.areaList[4].categoryName=="")return;
              let t1 =  this.dimensionMap.areaList.shift();
              this.dimensionMap.areaList.push(t1);
          }else if(this.col==2){
              try{
                if(this.dimensionMap.yearsList[5].categoryName==""){
                 $('.filterYear ul li').eq(7).css('opacity','0')
               }
              }catch(error){
                  console.log(error)
              }
              if(this.dimensionMap.yearsList[4].categoryName=="")return;
              let t1 =  this.dimensionMap.yearsList.shift();
              this.dimensionMap.yearsList.push(t1);
          }else if(this.col==3){
              if(this.dimensionMap.sortList[4].categoryName=="")return;
              let t1 =  this.dimensionMap.sortList.shift();
              this.dimensionMap.sortList.push(t1);
          }
      },TurnUp(){
          if(this.col==0){ // 
                try{
                    if(this.dimensionMap.contentList[6].categoryName==""){
                    $('.filterType ul li').css('opacity','1')
                    }
                }catch(error){
                    console.log(error)
                }
              if(this.dimensionMap.contentList[2].categoryName=="")return; // 
              let t1 =  this.dimensionMap.contentList.pop();
              this.dimensionMap.contentList.unshift(t1);
              if(this.categoryName == '' || localStorage.getItem('filtroTitleType')===""){
                for (let index = 0; index < this.dimensionMap.contentList.length; index++) {
                  const element = this.dimensionMap.contentList[index];
                  if(element.categoryName == $(".filterType li").eq(3).text()){
                    // this.dimensionMap.sortList.categoryId = this.dimensionMap.contentList[index-1].categoryId;
                    this.getYearsList(element.categoryName,'up');
                  }
                }
              }
          }else if(this.col==1){  // 
              try{
                if(this.dimensionMap.areaList[6].categoryName==""){
                 $('.filterLocal ul li').css('opacity','1')
               }
              }catch(error){
                console.log(error)
              }
              if(this.dimensionMap.areaList[2].categoryName=="")return;
              let t1 =  this.dimensionMap.areaList.pop();
              this.dimensionMap.areaList.unshift(t1);
          }else if(this.col==2){ // 
              try{
                if(this.dimensionMap.yearsList[6].categoryName==""){
                 $('.filterYear ul li').css('opacity','1')
               }
              }catch(error){
                  console.log(error)
              }
              if(this.dimensionMap.yearsList[2].categoryName=="")return;
              let t1 =  this.dimensionMap.yearsList.pop();
              this.dimensionMap.yearsList.unshift(t1);
          }else if(this.col==3){ // 
              if(this.dimensionMap.sortList[2].categoryName=="")return;
              let t1 =  this.dimensionMap.sortList.pop();
              this.dimensionMap.sortList.unshift(t1);
          }
      },noDefault(arg){
          if(arg==""||arg==""||arg==""||arg==""||arg==""||arg==""||arg==""){
              return true;
          }
          return false;
      },getkeyCode(bl){
          this.isShow = bl;
          this.col = 3;
          this.addCss();
      },
      rightMove(){
        //   
        try {
            this.screenTxt = this.screenTxt1+'/'+this.screenTxt2+'/'+this.screenTxt3+'/'+this.screenTxt4;
            let secreenType = 1;
            let traceId = c.getTrackPath();
            let traceType = c.getTrackPathType();
            let parentPageType = 1;
            let parentPageId = c.getParentPageId();
            bi.shaixuanResultClick(this.screenTxt,secreenType,traceId,traceType,parentPageType,parentPageId);
        }catch (error){
            console.log(error)
        }
      }
  },watch:{
     gather(){
      this.$nextTick(function(){
          if(this.catCode == undefined){
           this.catCode = window.localStorage.getItem('catCode');
          }
          let catCode = '';
          if(this.categoryName == '' || localStorage.getItem('filtroTitleType')===""){
              for (let index = 0; index < this.dimensionMap.contentList.length; index++) {
                  const element = this.dimensionMap.contentList[index];
                  if(element.categoryName == $(".filterType li").eq(3).text()){
                      this.dimensionMap.sortList.categoryId = this.dimensionMap.contentList[index].categoryId;
                      catCode = element.catCode;
                  }
              }
          }else{

              catCode = this.catCode
          }
        let reqStr = "&catCode="+catCode;
        let t1 = $(".filterType li").eq(3).text();
        let t2 = $(".filterLocal li").eq(3).text();
        let t3 = $(".filterYear li").eq(3).text();
        let t4 = $(".filterSort li").eq(3).text();
        console.log(t1);
        if(!this.noDefault(t1)){  // 
            let categoryId = 0;
            for (let index = 0; index < this.dimensionMap.contentList.length; index++) {
                const element = this.dimensionMap.contentList[index];
                if(element.categoryName==t1){
                    categoryId = this.dimensionMap.contentList[index].categoryId;
                    this.screenTxt1 = this.dimensionMap.contentList[index].categoryId;
                    break;
                }
                
            }
            if(this.categoryName == ''|| localStorage.getItem('filtroTitleType')===""){
                
            }else{
                reqStr +=("&content="+categoryId);
            }
        }
        if(!this.noDefault(t2)){ // 
            let categoryId = 0;
            for (let index = 0; index < this.dimensionMap.areaList.length; index++) {
                const element = this.dimensionMap.areaList[index];
                if(element.categoryName==t2){
                    categoryId = this.dimensionMap.areaList[index].categoryId;
                    this.screenTxt2 =  this.dimensionMap.areaList[index].categoryId;
                    break;
                }
            }
           // reqStr +=("&areaId="+categoryId); 
        }
         if(!this.noDefault(t3)){ // 
            let categoryId = 0;
            for (let index = 0; index < this.dimensionMap.yearsList.length; index++) {
                const element = this.dimensionMap.yearsList[index];
                if(element.categoryName==t3){
                    categoryId = this.dimensionMap.yearsList[index].categoryId;
                    this.screenTxt3 =  this.dimensionMap.yearsList[index].categoryId;
                    break;
                }
                
            }
            reqStr +=("&year="+categoryId);
        }
         if(!this.noDefault(t4)){ // 
            let categoryId = this.dimensionMap.sortList.categoryId;
            if(this.categoryName == '' || localStorage.getItem('filtroTitleType')===""){
                if(this.specialCode == 'YPYS'){
                    categoryId = this.dimensionMap.sortList.categoryId;
                }else{
                    for (let index = 0; index < this.dimensionMap.contentList.length; index++) {
                        const element = this.dimensionMap.contentList[index];
                        if(element.categoryName == t1){
                            categoryId = element.dimensionMap.sortList[0].categoryId;
                        }
                    }                     
                }
            }else{
            for (let index = 0; index < this.dimensionMap.sortList.length; index++) {
                const element = this.dimensionMap.sortList[index];
                if(element.categoryName==t4){
                    categoryId = this.dimensionMap.sortList[index].categoryId;
                    this.screenTxt4 =  this.dimensionMap.sortList[index].categoryId;
                    break;
                }
                
            }
            }
             reqStr +=("&sortType="+categoryId);
        }
        console.log(":"+reqStr);
        this.$emit('filterIssue',reqStr,this.screenTxt);
       });
     }
  }
}
</script>


