<!--  -->
<template>
  <div class="filter">
    <div class="filter-left">
      <h3>{{filtroTitleType}}</h3>
      <fr :cat-code="catCode" ref="fr" :category-name="categoryName" v-on:filterIssue="getSql" v-on:keepDo="toRight" v-on:kp="toRight2" v-if="isKeepAlive" :ty='ty' :topCatCode='topCatCode' :isVip='isVip' :specialCode="specialCode"></fr>
    </div>
    <div class="filter-right">
      <div class="top-box">
        <div class="box-left">
          <span :class="(btnClick?'button btnClick':'button')"><span class="jlt"></span></span>
          <span class="info"></span>
        </div>
        <div class="box-right" id="ttt">
          <span v-for="(item,index) in itemNavList" :key="index" :class="(index==itemNav?'hover':'')">{{item}}</span>
        </div>
      </div>
      <!--  -->
      <div class="active-numberJ">{{pageSize+1}}</div>
      <div class="wrap-box">
        <ul>
          <template v-for="(item,index) in assetList" >
            <li :class="(index==itemNo?'change':'')" :key="index" >
                <lazy-image
                  :src='item.assetImg'
                  :placeholder='placeholder'
                  :initScore = 'initScore'
                  :score = "item.score"
                  :imgClass="round"
                  :oragin = "item.providerId"
                    />
                <div class="listname"><div class="aJ" style="display: inline;height: 100%;">{{item.assetName}}</div><div class="bJ" style="display: inline;height: 100%;"></div></div>
            </li> 
          </template>
        </ul>
      </div>
        <!--  -->
        <div class="returnKey">
          
        </div>
        <!--  -->
      <div class="filter-scroll">
        <div class="filter-bar"></div>
      </div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"  ref="keyDo"></keyDo>
  </div>
</template>

<style scoped>
.listname {
  display: inline-block;
  position: relative;
  font-family: PingFangSC-Regular;
  font-size: 15px;
  color: #ffffff;
  text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 23px;
  margin-top: 11px;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.returnKey{
  position: absolute;
  bottom: 23px;
  right: 46px;
  width: 221px;
  height: 57px;
  border-radius: 15px;
  background: #FFBC02;
  border:1px solid #707070;
  box-shadow: 0 30px 40px rgba(0,0,0,.16);
  font-size: 22px;
  font-family: Helvetica Neue, Regular;
  color: #000000;
  line-height: 57px;
  display:none;
}
.returnKey::after{
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  left: 20px;
  bottom: -15px;
  background: #FFBC02;
}
.filter-scroll {
  position: absolute;
  height: 489px;
  top: 138px;
  right: 45.5px;
  width: 5px;
  background-color: rgba(255, 255, 255, 0.39);
  border-radius: 5px;
}
.filter-bar {
  position: relative;
  width: 10px;
  height: 140px;
  background-color: #ffffff;
  border-radius: 5px;
  left: -2px;
  top: 0;
}
.filter {
  width: 1280px;
  height: 720px;
  background: #283a4b;
  overflow: hidden;
}
.filter:after {
  clear: both;
  content: "";
  display: block;
}
.filter-left {
  width: 402px;
  height: 720px;
  background-color: #416186;
  padding-left: 71px;
  float: left;
  box-sizing: border-box;
}
h3 {
  margin-top: 25px;
  margin-bottom: 97px;
  font-size: 44px;
  color: #6687a7;
  line-height: 62px;
  font-family: "Arial", "Microsoft YaHei", "", "", sans-serif;
}
.wrap-box {
  width: 100%;
  height: 540px;
  overflow: hidden;
}
.active-numberJ {
  width: 100%;
  height: 28px;
  font-size: 20px;
  color: #ffffff;
  line-height: 28px;
  text-align: right;
  vertical-align: middle;
  box-sizing: border-box;
  padding-right: 28px;
}
.filter-right {
  position: relative;
  width: 878px;
  float: left;
  box-sizing: border-box;
  padding-left: 61px;
}
.top-box {
  display: flex;
  width: 100%;
  height: 50px;
  margin-top: 56px;
  justify-content: space-between;
}
.top-box .button {
    display: inline-block;
    width: 146px;
    height: 50px;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    border: 1px solid rgba(255, 255, 255, 1);
    color: rgba(255, 255, 255, 1);
    font-size: 20px;
    line-height: 50px;
    text-indent: 15px;
}
.top-box .btnClick{
  border: 3px solid #ffffff;
  background: #0098BC;
  margin: -2px;
  /* transform: scale(1.1); */
}
.top-box .box-right .focus{
  background: #0098BC;
  border: 2px solid #ffffff;
  margin: -2px;
  color: #ffffff !important;
}
.top-box .info {
  display: inline-block;
  color: rgba(255, 255, 255, 1);
  font-size: 20px;
  line-height: 28px;
  box-sizing: border-box;
  margin-left: 1px;
}
.top-box .box-right {
  margin-right: 100px;
  line-height: 50px;
  color: rgba(65, 97, 134, 1);
  font-size: 20px;
}
.top-box .box-right span:nth-of-type(1) {
  margin-right: 20px;
}
.top-box .box-right span {
  position: relative;
}
.top-box .box-right span {
    color: #416186;
    font-size: 20px;
    font-weight: 500;
    font-family: PingFang HK, Medium;
    /* border: 1px solid #fff; */
    width: 50px;
    height: 30px;
    display: inline-block;
    vertical-align: middle;
    line-height: 30px;
    text-align: center;
    border-radius: 8px;
}
.top-box .box-right .hover{
  color: #FFFFFF !important;
}
.top-box .box-right .hover::before {
  content: "";
  position: absolute;
  bottom: 32px;
  left: 0px;
  width: 100%;
  height: 0px;
  border: 2.5px solid rgba(255, 255, 255, 1);
  border-radius: 2px;
}
.top-box .box-right .hover::after {
  content: "";
  position: absolute;
  bottom: 24px;
  left: 44%;
  width: 0px;
  height: 0px;
  border: 4px solid transparent;
  border-top: 4px solid rgba(255, 255, 255, 1);
}
.top-box .box-right .active::before {
  content: "";
  position: absolute;
  bottom: -7px;
  left: -8px;
  width: 51px;
  height: 0px;
  border: 2px solid rgba(255, 255, 255, 1);
  border-radius: 2px;
}
.top-box .box-right .active::after {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 15px;
  width: 0px;
  height: 0px;
  border: 4px solid transparent;
  border-bottom: 4px solid rgba(255, 255, 255, 1);
}
.filter-right ul {
  position: relative;
}
.filter-right ul:after {
  clear: both;
  content: "";
  display: block;
}
.filter-right ul li {
  float: left;
  width: 160px;
  height: 230px;
  margin-left: 32px;
  margin-bottom: 40px;
  box-sizing: border-box;
  border: 1px solid rgba(149, 152, 154, 1);
  border-radius: 15px;
}
.filter-right ul li>div{
  margin-bottom: 0px;
}
.filter-right ul li:nth-of-type(4n + 1) {
  margin-left: 0px;
}
.filter-right ul li.change {
  box-sizing: border-box;
  /* transform: scale(1.1); */
  /* box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.55); */
  border: 3px solid #ffffff;
  /* border:3px solid #ffffff; */
  border-radius: 15px;
  box-sizing: border-box;
}
 .jlt {
    display: inline-block;
    vertical-align: middle;
    background: url(../../assets/icon.png) no-repeat -175px -245px;
    width: 35px;
    height: 35px;
    padding-right: -1px;
  }
</style>
<script>
import {mapGetters} from 'vuex';
import fr from "./filtro.vue";
import { HTTPUtil as api } from "../../fetch/api.js";
import {South as south} from '../../fetch/south.js'
import {bp as bi} from '../../js/ga.js';
import c from '../../js/common.js';
import {xha as x} from '../../js/xha.js'
import lodash from 'lodash';
import { debuglog } from 'util';
export default {
  props: ["catCode",'catId', "categoryName",'ty','isVip','title','specialName','specialCode','topCatCode'],
  data() {
    return {
      topCode:undefined,
      isShow: false,
      itemNo: -1,
      pageSize: 0,
      cheight: [],
      placeholder: require("../../assets/img_loading_160x230.png"),
      initScore: require("../../assets/corner_mark.png"),
      assetList: [],
      tempAssetList:[],
      tempAssetListLength:1,
      isKeepAlive: true,
      filtroTitleType:'',
      itemNum:'',
      firstLoad:true,
      screenTxt:'',
      btnClick:false,
      moveHeightArr:[],
      moveHeightIndex:0,
      tempSql:'',
      nav:false,
      itemNav:-1,
      itemNavList:['',''],
      returnTimes:0,
      round:'round',
      timer:null,
      zqCode:undefined,
      vipCode:undefined,
      pIndex:0,
      vip:'',//,
      pSize:8,
      switch:true,
      ajaxLoad:true,
    };
  },
  activated() {
    this.ajaxLoad = true; // 8
    this.getTitleType(this.catCode)
      if(this.categoryName !== '' && this.specialName == undefined){
        this.filtroTitleType = this.categoryName;
      }
    this.$store.commit('GET_HTTPRESPONSETIME',new Date().getTime());
    if (this.catCode != undefined) {
      this.topCode = undefined;
       window.localStorage.setItem('filtroTitleType',this.filtroTitleType);// catCode 
       // 
      if(this.ty){
          //  
        this.zqCode = this.ty;
        $('.box-right span:eq(1)').addClass('hover')
        $('.box-right span:eq(0)').removeClass('hover')
        // this.itemNavList = [];
        $('#ttt').css('display','none')
        window.localStorage.removeItem('ty')
        window.localStorage.setItem('ty',this.ty)
      }else{
        $('.box-right span:eq(0)').addClass('hover')
        $('.box-right span:eq(1)').removeClass('hover')
        this.zqCode = ""; // code
        this.itemNavList=['',''];
        $('#ttt').css('display','block')
        // $('.box-right span:eq(0)').addClass('hover')
        window.localStorage.removeItem('ty');
      }
      if(this.topCatCode != undefined){
        if(this.topCatCode == 'Demand'){
          // this.itemNavList = [];
          $('#ttt').css('display','none')
        }else{
          this.itemNavList=['',''];
          $('#ttt').css('display','block')
        }
        this.topCode = this.topCatCode;
        window.localStorage.removeItem('topCatCode');
        window.localStorage.setItem('topCatCode',this.topCatCode)
      }else{
        window.localStorage.removeItem('topCatCode');
      }
      this.vipCode = undefined;
      window.localStorage.removeItem('catCode');
      window.localStorage.setItem('catCode',this.catCode);
      window.localStorage.removeItem('specialCode');
      this.moveHeightArr=[];
      this.assetList = [];
      // this.tempAssetList = [];
      this.tempAssetListLength = 1;
      $('.returnKey').css('display','none');
      if(this.isVip == 1){
        this.vipCode = this.isVip;
        window.localStorage.removeItem('isVip');
        window.localStorage.setItem('isVip',this.isVip)
        // 
        // this.itemNav = 1;
        // this.vip = 1;
        // $('.box-right span:eq(1)').addClass('hover')
        // $('.box-right span:eq(0)').removeClass('hover');
        // this.itemNav = 0;
        this.vip = '';
      }else{
        // 
          // this.itemNav = 0;
          this.vip = '';
        $('.box-right span:eq(0)').addClass('hover')
        $('.box-right span:eq(1)').removeClass('hover');
      };
      $('.box-right span:eq(0)').removeClass('focus');
      $('.box-right span:eq(1)').removeClass('focus');
      this.btnClick = false;
      this.nav = false;
      this.isShow = false;
      this.isKeepAlive = false;
      $('.filter-bar').stop(false,true).animate({top:'0px'},200);
      this.itemNo = -1;
      this.pageSize = 0;
      this.cheight = [];
      let _this = this;
      this.$refs.fr.removeCss();
      this.$refs.fr.col = 0;
      setTimeout(() => {
        _this.isKeepAlive = true;
      }, 0);
      setTimeout(() => {
        _this.$refs.fr.col = 0;
        _this.$refs.fr.isShow = true;
        _this.$refs.fr.initData();
        if(this.title != undefined){
        this.filtroTitleType = this.title
        }
        _this.$refs.fr.addCss();
      }, 50);
    }else{
      if(window.localStorage.getItem('ty')){
        //  
        this.zqCode = window.localStorage.getItem('ty');
        // $('.box-right span:eq(1)').addClass('hover')
        this.vipCode = window.localStorage.getItem('isVip');
        // this.itemNavList = []
        $('#ttt').css('display','none')
        window.localStorage.setItem('ty',window.localStorage.getItem('ty'))
      }else{
        this.itemNavList=['','']
        $('#ttt').css('display','block')
      };
      if(window.localStorage.getItem('topCatCode')){
        this.topCode = window.localStorage.getItem('topCatCode');
        if(window.localStorage.getItem('topCatCode') == 'Demand'){
          // this.itemNavList = [];
          $('#ttt').css('display','none')
        }else{
          this.itemNavList=['','']
          $('#ttt').css('display','block')
        }
      }
      if(window.localStorage.getItem('specialCode') !== undefined){
        this.specialCode = window.localStorage.getItem('specialCode');
      }else{
        this.specialCode = undefined;
      };
      if(window.localStorage.getItem('isVip') !== null){
        this.isVip = '1';
      };
      this.catCode = window.localStorage.getItem('catCode');
      this.filtroTitleType = window.localStorage.getItem('filtroTitleType');
    }
  },
  updated(){
    this.$nextTick(function(){
      this.$lazyImages.loadImage();
    })
  },
  mounted() {
  },beforeRouteEnter (to, from, next) {
        window.sessionStorage.removeItem("srcPagePath");
        window.sessionStorage.setItem("srcPagePath", location.origin+'/henan'+from.path);
        if(from.name!=null){
          const zqback =  localStorage.getItem('fliterback');
          if(zqback){
            if(zqback==="hzindex"){
              localStorage.setItem('fliterback',"hzindex");
            }
          }
          if(from.name==="hzindex"){
             localStorage.setItem('fliterback',from.name);
          }
        }
        next();
  },
  methods: {      // 
    screenClick(){
     try {
      let screenTxt = this.screenTxt;
      let clickId = this.assetList[this.itemNo].assetId;
      let traceId = c.getTrackPath();
      let traceType = c.getTrackPathType();
      let parentPageType = 1;
      let parentPageId = c.getParentPageId();
      let specialType = '';
      bi.shaixuan(screenTxt,traceId,traceType,parentPageType,parentPageId,specialType,clickId)
      console.log(''+clickId)
     }catch (error){
       console.log(error)
     }
    },
      // 
    pageLode(){
      let SrcPagePath = this.filtroTitleType;
      let PageName = this.assetList[this.itemNo].assetName;
      let AsssetID = this.assetList[this.itemNo].assetId;
      let Id = '0';
      let TYPE = "0"+(this.navpos+1);
      x.pageLoad("",SrcPagePath,PageName,AsssetID,Id,TYPE);
    },
    keyCode(kc) {
      if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "KeyEnter") {
        this.clearTimer();
        if(this.specialCode !== '' && this.specialCode !== undefined && this.specialCode !== null){
            window.localStorage.removeItem('specialCode');
            window.localStorage.setItem('specialCode',this.specialCode);
        }
        if(this.btnClick){
          window.localStorage.removeItem('filtroTitleType')
          window.localStorage.setItem('filtroTitleType',this.filtroTitleType) 
           this.$router.push({name:'search',params: {type:'filter'}});
           return;
        }
        if(this.itemNo == -1)return;
        this.screenClick()
        let url = this.assetList[this.itemNo].jsonUrl;
        window.localStorage.removeItem('filtroTitleType')
       try{
          if(this.ty){
              this.zqCode = this.ty
              window.localStorage.removeItem('ty');
              window.localStorage.setItem('ty',this.ty)
            }else{
              // 
              // window.localStorage.removeItem('ty');
            }
       }catch(error){
          console.log(error)
       }
        window.localStorage.setItem('filtroTitleType',this.filtroTitleType);  
        this.pageLode();
        window.localStorage.setItem('EnterType','2');
        this.$router.push({ name: "detail", params: { jsonUrl: url } });
      } else if (kc == "KeyBack") {
        this.clearTimer();
        if(this.pageSize>0){
          this.itemNo = 0;
          this.ajaxLoad = true; // 8
          $('.wrap-box ul').css('top','0px');
          $(".filter-bar").stop(false, true).animate({ top: '0px' });
          this.pIndex = 0;
          this.pageSize = 0;
          this.moveHeightArr = [];
          this.moveHeightIndex = 0;
          this.tempAssetListLength = 1;
          // this.assetList = this.tempAssetList[this.tempAssetListLength-1];
          // let tempList = [];
          // tempList.push(this.tempAssetList[0]);
          // this.tempAssetList = tempList;
          return; 
        }else{
          this.toRight2(false,'goback');
        }
      }
    },
    getSql(sql,txt) {
      this.screenTxt = txt;
      this.sql = sql;
      this.pIndex = 0;
      this.pageSize = 0;
      if(this.isSouth){
          if(this.specialCode !== undefined && this.specialCode !== '' && this.specialCode !== null){
            //  
              if(!$('.box-right span:eq(0)').hasClass('hover')){
                south
                .getFilterResult(sql, this.pIndex, this.pSize,this.specialCode,this.vipCode)
                .then(jsonData => {
                  this.assetList = [];
                  if(jsonData.data == null)return;
                  this.assetList = jsonData.data.assetList;
                  this.itemNum = jsonData.data.assetNum;
                  // this.tempAssetList = []; // 
                  this.tempAssetListLength = 1 // 
                  // this.tempAssetList[0] = jsonData.data.assetList;
                });
              }else{
                south
                .getFilterResult(sql, this.pIndex, this.pSize,this.specialCode)
                .then(jsonData => {
                  this.assetList = [];
                  if(jsonData.data == null)return;
                  this.assetList = jsonData.data.assetList;
                  this.itemNum = jsonData.data.assetNum;
                  // this.tempAssetList = []; // 
                  this.tempAssetListLength = 1 // 
                  // this.tempAssetList[0] = jsonData.data.assetList;
                });
              }
          }else{
            //   
                south
                .getFilterResult(sql, this.pIndex, this.pSize,this.zqCode,this.vipCode,this.specialCode,this.topCode)
                .then(jsonData => {
                  this.assetList = [];
                  if(jsonData.data == null)return;
                  this.assetList = jsonData.data.assetList;
                  this.itemNum = jsonData.data.assetNum;
                  // this.tempAssetList = []; // 
                  this.tempAssetListLength = 1 //  
                  // this.tempAssetList[0] = jsonData.data.assetList;
                });
          }
      }else{
       if(this.specialCode !== undefined && this.specialCode !== '' && this.specialCode !== null){
        //   
            if(!$('.box-right span:eq(0)').hasClass('hover')){
              api
              .getFilterResult(sql, this.pIndex, this.pSize,this.specialCode,this.vipCode)
              .then(jsonData => {
                this.assetList = [];
                if(jsonData.data == null)return;
                this.assetList = jsonData.data.assetList;
                this.itemNum = jsonData.data.assetNum;
                // this.tempAssetList = []; // 
                this.tempAssetListLength = 1 // 
                // this.tempAssetList[0] = jsonData.data.assetList;
              });              
            }else{
              api
              .getFilterResult(sql, this.pIndex, this.pSize,this.specialCode)
              .then(jsonData => {
                // this.vipCode
                this.assetList = [];
                if(jsonData.data == null)return;
                this.assetList = jsonData.data.assetList;
                this.itemNum = jsonData.data.assetNum;
                // this.tempAssetList = []; // 
                this.tempAssetListLength = 1 // 
                // this.tempAssetList[0] = jsonData.data.assetList;
              });
            }
       }else{
        //   
            api
            .getFilterResult(sql, this.pIndex, this.pSize,this.zqCode,this.vipCode,this.specialCode,this.topCode)
            .then(jsonData => {
              this.assetList = [];
              if(jsonData.data == null)return;
              this.assetList = jsonData.data.assetList;
              this.itemNum = jsonData.data.assetNum;
              // this.tempAssetList = []; // 
              this.tempAssetListLength = 1 // 
              // this.tempAssetList[0] = jsonData.data.assetList;
            });
       }
      }
    },
    down:lodash.debounce(function(){
      this.clearTimer();
      if ($(".filter-bar").is(":animated")) return;
      if(this.assetList.length==0)return;
      if(this.nav && this.btnClick){
        this.itemNo = 0;
        this.btnClick = false;
        this.nav = false;
        this.scrollName();
        return;
      };
      if(this.itemNo == -1){
          if(this.nav && $('.box-right span:eq(0)').hasClass('focus')){
            $('.box-right span:eq(0)').removeClass('focus')
            $('.box-right span:eq(0)').addClass('hover');
            this.itemNo = 0;
            this.itemNav = 0;
            this.nav = false;
            this.vip = '';
            this.scrollName();
            return;
          };
          if(this.nav && $('.box-right span:eq(1)').hasClass('focus')){
            $('.box-right span:eq(1)').removeClass('focus')
            $('.box-right span:eq(1)').addClass('hover');
            this.itemNav = 1;
            this.itemNo = 0;
            this.nav = false;
            this.scrollName();
            this.vip = 1;
            return;
          };
      }
      if(this.itemNo >=4 && this.assetList.length < 8){
        return;
      }
      if(this.itemNo < 4 && (this.itemNo+4) > this.assetList.length-1){
        return;
      }
      if(this.itemNo >= 4 && this.itemNo <8 && this.assetList.length == 8){
        if(this.ajaxLoad){
          this.pIndex += 8;
          this.getNextPage('down');
        }
      }else if(this.itemNo < 4 && this.itemNo >= 0){
        this.itemNo += 4;
        this.scrollName(); 
        }     
    },100),
    up:lodash.debounce(function(){
       this.clearTimer();
      if(this.btnClick || $('.box-right span:eq(1)').hasClass('focus') || $('.box-right span:eq(0)').hasClass('focus'))return;
