    scrollName(){
      // 
          let p_title = $('.wrap-box ul li:eq('+this.itemNo+') .listname .aJ').text()
        if (p_title.length >= 10) {
          let speed = 40;
          let that = this;
          let tab = $('.wrap-box ul li:eq('+this.itemNo+') .listname')[0];
          console.log(":"+$('.wrap-box ul li:eq('+this.itemNo+') .listname .aJ').text());
          let tab1 = $('.wrap-box ul li:eq('+this.itemNo+') .listname .aJ')[0];
          let tab2 =  $('.wrap-box ul li:eq('+this.itemNo+') .listname .bJ')[0];
          tab.style.textOverflow = "clip";
          tab2.innerHTML = tab1.innerHTML;
          this.timer = setInterval(function() {
            that.Marquee(tab, tab1, tab2);
          }, speed);
        }
    },
    Marquee(tab, tab1, tab2) {
      if (tab2.offsetWidth - tab.scrollLeft <= 0) {
        // tab.scrollLeft -= tab1.offsetWidth;
        tab.scrollLeft = 0; // 
      } else {
        tab.scrollLeft++;
      }
    },
    clearTimer(){
      // 
        if(this.timer != null){
          console.log('')
          clearInterval(this.timer)
          let tab = $('.wrap-box ul li:eq('+this.itemNo+') .listname')[0];
          $('.wrap-box ul li:eq('+this.itemNo+') .listname .bJ').text("");
          tab.scrollLeft = 0;
          tab.style.textOverflow = 'ellipsis';
          this.timer = null;
