    scroll(pageSize) {
      if(this.itemNum != ''){
        if (this.itemNum*1 >= 8) {
          let ratio = 8 / (this.itemNum*1);
          let distance = 349 * ratio;
          $(".filter-bar").stop(false, true).animate({top: distance * pageSize <= 1 ? "-1" : distance * pageSize + "px"});
        }
      }
