          this.filtroTitleType = '';
          this.catCode = 'series'
        };
         if(type === 'Comic'){
          this.filtroTitleType = '';
          this.catCode = 'Comic'
        };
         if(type === 'Entertainment'){
          this.filtroTitleType = '';
          this.catCode = 'Entertainment'
        };
         if(type === 'record'){
          this.filtroTitleType = '';
          this.catCode = 'record'
