          bi.loadPage(traceId,traceType,9,0,1,this.catId,'',overTime);
      })
    }
  },
  beforeRouteLeave(to, from, next) {
    try {
         let overTime = 0;
         overTime =  new Date().getTime() - this.httpResponseTime;
          let traceId = c.getTrackPath();
         let traceType = "|1|9|";
         bi.closPage(traceId,traceType,9,0,'',overTime)
         console.log(""+overTime+"^^^^^^^focusId:"+this.catId);
    } catch (error) {
        console.log(error);
    } finally {
      next()
    }
  },
  computed:{
    ...mapGetters([
      'rootPath',
      'catList',
      'category',
      'behindParams',
      'isHTTPS',
      'httpRequestTime',
      'httpResponseTime',
      'isSouth',
      'navpos'
    ])
  }
};
</script>



