<template>
  <div class="box">
    <div class="historical">
      <div class="tit">{{title}}</div>
      <div class="content" id="d1">
        <div class="writediv changebac" id="b1"></div>
        <ul class="cul" id="c0">
          <li class="text_n" id="jv">{{conten.acc}}</li>
          <li class="text_n" id="sc">{{conten.his}}</li>
        </ul>
      </div>
      <div class="content right_con" id="d2">
        <div class="writediv" id="b2"></div>
        <ul class="cul" v-if="acc" id="c1">
          <li  class="text_n" v-for="(item,index) in sc"  :key="item.id" :id="('j'+index)">
            {{item}}
          </li>
        </ul>
        <ul class="cul" v-if="add" id="c1">
          <li  class="text_n" v-for="(item,index) in jv"  :key="item.id" :id="('j'+index)">
            {{item}}
          </li>
        </ul>
      </div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
    <hCenter ref="hCenter" v-on:Center="Center" v-on:itnum="itnumsIng" :jsonUrl="urls"
             :Tvnum="itnums" v-on:getjson ='sendJson' :bp="bp" :acc="acc" ></hCenter>
    <CFirm ref="CFirm" v-on:listernpopup="firm"></CFirm>
  </div>
</template>
<style scoped="historical">
.box:after {
  clear: both;
  content: "";
  display: block;
}
.historical {
  width: 333px;
  height: 720px;
  background-color: #416186;
  border-top: 1px solid #a8977c;
  float: left;
}
.tit {
  width: 220px;
  height: 62px;
  font-size: 44px;
  text-align: center;
  line-height: 62px;
