  margin-left: 63px;
}
.content {
  float: left;
  width: 135px;
  height: 70%;
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0.01) 0%,
    rgba(255, 255, 255, 0.2) 50%,
    rgba(255, 255, 255, 0.01) 100%
  );
  position: relative;
  margin-top: 39px;
  margin-left: 56px;
}
.content.right_con {
  float: left;
  width: 114px;
  height: 70%;
