    to bottom,
    rgba(255, 255, 255, 0.01) 0%,
    rgba(255, 255, 255, 0.2) 50%,
    rgba(255, 255, 255, 0.01) 100%
  );
  position: relative;
  margin-top: 39px;
  margin-left: 2px;
  overflow: hidden;
}
.content ul {
  width: 100%;
  position: absolute;
  left: 0;
  top: 210px;
}
.content ul .text_n {
  color: rgba(255, 255, 255, 0.7);
  width: 100%;
  height: 68px;
  font-size: 26px;
  text-align: center;
  line-height: 70px;
}
.content .writediv {
  width: 100%;
  height: 68px;
  background: rgba(255, 255, 255, 0.4);
  position: absolute;
  left: 0;
  top: 210px;
  border-radius: 15px;
}
.content .changebac {
  background: #0098bc;
  border: 3px solid #fff;
  box-sizing: border-box;
}
.content.active {
  background: rgba(255, 255, 255, 0.4);
  border: 1px solid white;
  border-radius: 15px;
}
</style>
