          layout: "Detail_Movie",
          layoutUrl:
            "https://aged-bds.yanhuamedia.tv/?s=17|6&p=yhAssetDetail&k=1&v=1&assetId=1188&c=6",
          movieLength: "5383840",
          playurl:
            "http://vcdn.yanhuamedia.tv/vod/201608/02/16/35/201608021635092423je9qwvq.ts",
