        }
      ],
      shouye: false
    };
  },
  created() {
    this.load();
  },
  activated() {
    if (this.bp != undefined) {
      console.log("");
      this.urls = [];
      this.itnums = [];
      this.isShow = true;
      this.op = 0;
      this.itemNo = 0;
      $("#b2").removeClass("changebac"); // 
      $("#d2").removeClass("active"); // 
      if (this.bp.type !== undefined) {
        this.ifHistory(this.bp.type);
      } else {
        this.ifHistory(false);
      }
    } else {
      if (this.num == 0) {
        console.log("");
        this.urls = [];
        this.itnumsIng();
        this.sendJson();
      }
    }
  },
  updated() {
    this.$nextTick(function() {
      this.$lazyImages.loadImage();
    });
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        // this.KeyEnter();
      } else if (kc == "left") {
        this.left();
      }
    },
    ifHistory(show) {
      if (show) {
        console.log("");
        this.num = 0;
        $("#c" + this.op).css({
          transition: "all 0.3s",
          transform: "translateY(-" + 70 * this.num + "px)"
        });
        $("#c" + (this.op + 1)).css({
          transition: "all 0.3s",
          transform: "translateY(-" + 68 * this.itemNo + "px)"
        });
        this.add = false;
        this.acc = true;
        this.itnumsIng();
        this.sendJson();
      } else {
        console.log("");
        this.num = 1;
        $("#c" + this.op).css({
          transition: "all 0.3s",
          transform: "translateY(-" + 70 * this.num + "px)"
        });
        $("#c" + (this.op + 1)).css({
          transition: "all 0.3s",
          transform: "translateY(-" + 68 * this.itemNo + "px)"
        });
        this.add = true;
        this.acc = false;
        this.historyJson();
      }
      this.addBcss();
    },
    load() {
      if (this.acc) {
        this.pageType = 5;
      } else {
        this.pageType = 6;
      }
      try {
        c.setTrackPath(0, this.pageType);
        let traceId = c.getTrackPath();
        let AsssetID = "";
        let traceType = c.getTrackPathType();
        let catId = c.getParentPageId();
        let catCode = c.getParentPageType();
        this.itTime = new Date().valueOf();
        let PageName = "";
        let Id = "";
        let TYPE = "";
        let Reserve1 = "";
        let oldTime =
          performance.timing.domContentLoadedEventEnd -
          performance.timing.navigationStart;
        bi.loadPage(
          traceId,
          traceType,
          this.pageType,
          "",
          catCode,
          catId,
          oldTime
        );
      } catch (error) {
        console.log(error);
      }
    },
    closPages() {
      if (this.acc) {
        this.pageType = 5;
      } else {
        this.pageType = 6;
      }
      let time = new Date().valueOf();
      let times = time - this.itTime;
      let traceId = c.getTrackPath();
      let traceType = c.getTrackPathType();
      bi.closPage(traceId, traceType, this.pageType, 0, times);
    },
    Center(pos, url, actor, clear) {
      if(pos=="back"){
        if(this.prevPathName){
          console.log("");
          this.$router.push({name:this.prevPathName});
        } else{
          this.$router.go(-1);
        }   
        return 0; 
      }
      if (pos == "left") {
        this.isShow = true;
        this.addBcss();
      }
      if (pos == "KeyEnter") {
        if (clear == "clear") {
          this.isShow = true;
          this.addBcss();
          if (this.num == 0) {
            localStorage.removeItem("PersonMovies");
            localStorage.removeItem("PersonInfo");
            this.urls = [];
          } else {
            if (this.isSouth) {
              localStorage.removeItem("historyArr");
            } else {
              yh.removeHistory();
            }
            this.urls = [];
            this.itnums = [];
          }
        } else {
          this.$refs.CFirm.updateStast(url, "KeyEnter", this.num, 8, actor);
        }
      }
      if (pos == "json") {
        this.sendJson();
      }
    },
    back() {
      if(this.prevPathName){
         console.log("");
         this.$router.push({name:this.prevPathName});
      } else{
        this.$router.go(-1);
      }    
    },
    addBcss() {
      $("#b" + (this.op + 1)).addClass("changebac");
      $("#d" + (this.op + 1)).addClass("active");
    },
    removeBcss() {
      $("#b" + (this.op + 1)).removeClass("changebac");
      $("#d" + (this.op + 1)).removeClass("active");
    },
    left() {
      if (this.op == 1) {
        this.removeBcss();
        this.op--;
        this.addBcss();
        //  this.urls = [];
      }
    },
    right() {
      if (this.op == 0) {
        this.removeBcss();
        this.op++;
        this.addBcss();
        if (this.num == 0) {
          this.sendJson();
        } else {
          this.historyJson();
        }
      } else {
        if (!this.urls.length) return;
        this.isShow = false;
        this.removeBcss();
        this.$refs.hCenter.updateStast("right");
      }
    },
    down() {
      this.urls = [];
      if (this.op == 0) {
        this.add = true;
        this.acc = false;
        this.num++;
        this.itemNo = 0;
        if (this.num >= 2) {
          this.num = 1;
        }
        $("#c" + this.op).css({
          transition: "all 0.3s",
          transform: "translateY(-" + 70 * this.num + "px)"
        });
        $("#c" + (this.op + 1)).css({
          transition: "all 0.3s",
          transform: "translateY(-" + 68 * this.itemNo + "px)"
        });
        if (this.num == 0) {
          this.itnumsIng();
        } else {
          this.historyJson();
        }
      } else {
        if (this.num == 0) {
          this.itemNo++;
          if (this.itemNo > 4) {
            this.itemNo = 4;
          }
          $("#c" + this.op).css({
            transition: "all 0.3s",
            transform: "translateY(-" + 68 * this.itemNo + "px)"
          });
          this.sendJson();
          this.itnumsIng();
        } else {
          this.itemNo++;
          if (this.itemNo > 2) {
            this.itemNo = 3;
          }
          $("#c" + this.op).css({
            transition: "all 0.3s",
            transform: "translateY(-" + 68 * this.itemNo + "px)"
          });
          this.historyJson();
        }
      }
    },
    up() {
      this.urls = [];
      if (this.op == 0) {
        this.acc = true;
        this.add = false;
        this.num--;
        this.itemNo = 0;
        if (this.num < 0) {
          this.num = 0;
        }

        $("#c" + this.op).css({
          transition: "all 0.3s",
          transform: "translateY(-" + 70 * this.num + "px)"
        });
        $("#c" + (this.op + 1)).css({
          transition: "all 0.3s",
          transform: "translateY(-" + 68 * this.itemNo + "px)"
        });
        if (this.num == 0) {
          this.sendJson();
          this.itnumsIng();
        } else {
          this.historyJson();
        }
      } else {
        if (this.num == 0) {
          this.itemNo--;
          if (this.itemNo < 0) {
            this.itemNo = 0;
          }
          $("#c" + this.op).css({
            transition: "all 0.3s",
            transform: "translateY(-" + 68 * this.itemNo + "px)"
          });
          this.sendJson();
          this.itnumsIng();
        } else {
          this.itemNo--;
          if (this.itemNo < 0) {
            this.itemNo = 0;
          }
          $("#c" + this.op).css({
            transition: "all 0.3s",
            transform: "translateY(-" + 68 * this.itemNo + "px)"
          });
          this.historyJson();
        }
      }
    },
    screen(jsonDa, jsonDb) {
      //      jsonDa,jsonDb
      let jsonObj = [];
      if (jsonDb) {
        for (let i = 0; i < jsonDb.length; i++) {
          if (jsonDb[i].assetType == jsonDa) {
            jsonObj.unshift(jsonDb[i]);
          }
        }
      }
      return jsonObj;
    },
    itnumsIng() {
      if (this.num == 1) {
        // 
        console.log("");
        this.historyJson();
        return false;
      }
      let movieTv = JSON.parse(localStorage.getItem("PersonMovies")); //        
      let special = JSON.parse(localStorage.getItem("PersonInfo")); //        
      let zhuanti;
      if (special) {
        zhuanti = special.length;
      } else {
        zhuanti = 0;
      }
      let actor = this.screen("", movieTv);
      let Series = this.screen("Series", movieTv);
      let Movie = this.screen("Movie", movieTv);
      let zongyi = this.screen("Column", movieTv);
      let arr = [];
      if (this.itemNo == 0) {
        arr = [0, Series.length, 0, 0, 0];
      }
      if (this.itemNo == 1) {
        arr = [0, 0, Movie.length, 0, 0];
      }
      if (this.itemNo == 2) {
        arr = [0, 0, 0, 0, actor.length];
      }
      if (this.itemNo == 3) {
        arr = [0, 0, 0, zongyi.length, 0];
      }
      if (this.itemNo == 4) {
        arr = [zhuanti, 0, 0, 0, 0];
      }
      this.itnums = arr;
    },
    hisLengh(num) {
      let len;
      if (num) {
        len = num;
      } else {
        len = 0;
      }
      return len;
    },
    historyJson() {
      if (this.isSouth) {
        this.jsontyb = JSON.parse(window.localStorage.getItem("historyArr"));
      } else {
        this.jsontyb = JSON.parse(yh.queryHistory());
      }
      let hisTV = this.changeJson(this.jsontyb);
      if (!hisTV) return;
      let one = [];
      let two = [];
      let there = [];
      let four = [];
      for (let i = 0; i < hisTV.length; i++) {
        if (this.Data(hisTV[i].recordTime) == 0) {
          one.push(hisTV[i]);
        }
        if (this.Data(hisTV[i].recordTime) == 1) {
          two.push(hisTV[i]);
        }
        if (this.Data(hisTV[i].recordTime) == 2) {
          there.push(hisTV[i]);
        }
        if (this.Data(hisTV[i].recordTime) > 2) {
          four.push(hisTV[i]);
        }
      }
      if (this.itemNo == 0) {
        this.urls = one;
        this.lishiarr(one);
      }
      if (this.itemNo == 1) {
        this.urls = two;
        this.lishiarr(two);
      }
      if (this.itemNo == 2) {
        this.urls = there;
        this.lishiarr(there);
      }
      if (this.itemNo == 3) {
        this.urls = four;
        this.lishiarr(four);
      }
    },
    lishiarr(num) {
      let Series = this.screen("Series", num);
      let Movie = this.screen("Movie", num);
      let zongyi = this.screen("Column", num);
      let arr = [
        0,
        this.hisLengh(Series.length),
        this.hisLengh(Movie.length),
        this.hisLengh(zongyi.length),
        0
      ];
      this.itnums = arr;
    },
    changeJson(jsonDB) {
      //        
      let jsontyb = jsonDB;
      let _tempArray = []; //  sort windows
      let sortedObjKeys = Object.keys(jsontyb).sort(function(a, b) {
        return jsontyb[b].recordTime - jsontyb[a].recordTime;
      });
      for (let i in sortedObjKeys) {
        if (sortedObjKeys.hasOwnProperty(i)) {
          _tempArray.push(jsontyb[sortedObjKeys[i]]);
        }
      }
      return _tempArray;
    },
    Data(gettime) {
      let newtime = new Date(new Date().setHours(0, 0, 0, 0));
      var timestamp = Date.parse(newtime);
      let timeout = Math.ceil((timestamp - gettime) / 86400000);
      //        console.log("gettime:============"+timeout);
      return timeout;
    },
    sendJson() {
      //        
      //        
      //        num  itemNo
      if (this.num == 0) {
        let movieTv = JSON.parse(localStorage.getItem("PersonMovies"));
        //        
        let special = JSON.parse(localStorage.getItem("PersonInfo"));
        if (!special) {
          special = [];
        }
        //          
        if (this.itemNo == 0) {
          //            
          this.urls = this.screen("Series", movieTv);
        } else if (this.itemNo == 1) {
          //            
          this.urls = this.screen("Movie", movieTv);
        } else if (this.itemNo == 2) {
          //            
          this.urls = this.screen("", movieTv);
        } else if (this.itemNo == 3) {
          //            
          this.urls = this.screen("Column", movieTv);
        } else if (this.itemNo == 4) {
          //            
          this.urls = special;
        }
      }
    },
    firm(bl, name, pos) {
      if (!bl) {
        this.$refs.hCenter.updateStast(pos);
        if (name == 0) {
          this.itnumsIng();
        } else {
          this.historyJson();
        }
      }
    },
    updateStast(bl, pos) {
      this.isShow = bl;
      this.pos = pos;
    }
  },
  components: {
    hCenter,
    CFirm
  },
  computed: {
    ...mapGetters(["isSouth"])
  },
  watch: {
   
  },
  beforeRouteEnter(to, from, next) {
    console.log(from);
    next(vm => {
      //  `vm` 
      if(from.name!=null && from.name!='detail' && from.name!='actor' && from.name!='Special'&& from.name!='AllSpecial' ){
        vm.prevPathName = from.name;
      }
      window.sessionStorage.removeItem('srcPagePath');
      window.sessionStorage.setItem('srcPagePath',location.origin+'/henan'+from.path);
    })
    // if(from.name!=null){
    //   this.prevPathName = from.name;
    // }
    // window.sessionStorage.removeItem('srcPagePath');
    // window.sessionStorage.setItem('srcPagePath',location.origin+'/henan'+from.path);
    // next();
  }
};
</script>



